import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators
} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import { DialogService } from '../../../services/dialog.service';
import { GroupService } from '../../../services/group.service';
import {Helper} from '../../../common/helper';
import { Subscription } from 'rxjs/Rx';
import { ActivatedRoute } from '@angular/router';
import {DepartmentUserSettings} from '../../../models/gr/department-user-settings';
import {EditGroupOutsideSetting} from '../../../models/gr/edit-group-outside-settings';
import {DialogResult} from '../../../models/dialog-param';
import {GroupOutsideSaveResult} from '../../../models/gr/group-outside-save-result';
import {HttpStatusCode} from '../../../common/http_status_code';
import {CreateHospitalGroupSettingsSendApi} from '../../../models/gr/create-hospital-group-settings-send-api';
import {HttpError} from '../../../common/error/http.error';

@Component({
  selector: 'app-gr0007-page',
  templateUrl: './gr0007-page.component.html',
  styleUrls: ['./gr0007-page.component.scss']
})
export class Gr0007PageComponent implements OnInit {

  subscription: Subscription;
  groupId: string;
  model = new EditGroupOutsideSetting();
  myForm: FormGroup;
  mailForm: FormGroup;
  listIshaOutside = [];
  listDe = [];
  officeUser: any = {};

  listUser = [];
  selectedUserRow = [];
  selectedUserRowOutside = [];
  listSelectedUser = [];
  listSelectedUserOutside = [];
  listMember = [];
  listSelectedMember = [];
  selectedMemberRow = [];
  listAdmin = [];
  inviteFlg = [];
  checkAllUser = false;
  checkAllUserOutside = false;
  nonSelectedAdmin = false;
  showOutsideUser = [];
  checkBackItemInside = '';
  checkBackItemOutside = '';
  provisional = [];

  checkSearch: boolean;
  validationForm: any = {};
  validationMess: any = {};
  formErrors = {'name': '', 'mail': ''};

  constructor(private groupService: GroupService,
              private translate: TranslateService,
              private fb: FormBuilder,
              private dialogService: DialogService,
              public helper: Helper,
              public activatedRoute: ActivatedRoute) {
    this.myForm = this.fb.group({'name': ['']});
    this.mailForm = this.fb.group({'mail': ['']});
  }

  ngOnInit() {
    this.callApiInit();
  }

  callApiInit() {
    this.subscription = this.activatedRoute.params.subscribe(params => {
      this.groupId = params['groupId'];
    });
    this.dialogService.setLoaderVisible(true);
    this.groupService.getEditGroupOutside(this.groupId).subscribe((settings: EditGroupOutsideSetting) => {
      this.dialogService.setLoaderVisible(false);
      this.model = settings;
      this.editFormOutsideGroup();
      this.genDepartment();
      this.getListUser();
    },
    (error: HttpError) => {
      if (error.contains('COMMON.SAVE_FAILED')) {

      } else {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('MSG.ERROR');
      }
    });
  }

  getListUser() {
    const listAll = this.model.prepareOutsideOfficeUsers;
    for (let l = 0; l < listAll.length; l++) {
      if (listAll[l].officeName === '') {
        this.listUser.push(listAll[l]);
      }
      for (let m = 0; m < this.model.group.users.length; m++) {
        if (listAll[l].userId === this.model.group.users[m].userId) {
          this.listMember.push(listAll[l]);
        }
      }
    }
    this.listUser = this.helper.convertOutsideUserName([], this.listUser, 'inside');
    this.listMember = this.helper.convertOutsideUserName([], this.listMember, 'inside');
    for (let i = 0; i < this.listUser.length; i++) {
      for (let j = 0; j < this.model.group.users.length; j++) {
        if (this.listUser[i].userId === this.model.group.users[j].userId) {
          this.listUser.splice(i, 1);
        }
      }
    }
    for (let a = 0; a < listAll.length; a++) {
      for (let b = 0; b < this.model.group.users.length; b++) {
        if (listAll[a].userId === this.model.group.users[b].userId) {
          if (this.model.group.users[b].inviteFlg === true) {
            this.inviteFlg.push(true);
          } else if (this.model.group.users[b].inviteFlg === false) {
            this.inviteFlg.push(false);
          }
          if (this.model.group.users[b].authority === 'MEMBER') {
            this.listAdmin.push(false);
          } else if (this.model.group.users[b].authority === 'ADMIN') {
            this.listAdmin.push(true);
          }
        }
      }
    }
  }

  genDepartment() {
    const step = 1;
    this.listDe = this.helper.createArrayDepartment([], this.model.departments, '', step);
  }

  changeDepId(val) {
    const lists = [];
    for (let l = 0; l < this.model.prepareOutsideOfficeUsers.length; l++) {
      if (this.model.prepareOutsideOfficeUsers[l].officeName === '') {
        lists.push(this.model.prepareOutsideOfficeUsers[l]);
      }
    }
    this.checkBackItemInside = val;
    if (val === '') {
      this.listUser = this.helper.convertOutsideUserName([], lists, 'inside');
      for (let i = 0; i < this.listUser.length; i++) {
        for (let j = 0; j < this.listMember.length; j++) {
          if (this.listUser[i].userId === this.listMember[j].userId) {
            this.listUser.splice(i, 1);
          }
        }
      }
    }else {
      const list = [];
      this.listUser = this.helper.convertOutsideUserName([], lists, 'inside');
      for (let v = 0; v < this.listUser.length; v++) {
        if (this.listUser[v].depId === val) {
          list.push(this.listUser[v]);
        }
      }
      this.listUser = list;
      for (let i = 0; i < this.listUser.length; i++) {
        for (let j = 0; j < this.listMember.length; j++) {
          if (this.listUser.length > 0) {
            if (this.listUser[i].userId === this.listMember[j].userId) {
              this.listUser.splice(i, 1);
            }
          }
        }
      }
    }
  }

  changeOfficeId(value) {
    const lists = [];
    for (let l = 0; l < this.model.prepareOutsideOfficeUsers.length; l++) {
      if (this.model.prepareOutsideOfficeUsers[l].officeName !== '') {
        lists.push(this.model.prepareOutsideOfficeUsers[l]);
      }
    }
    this.checkBackItemOutside = value;
    if (value === '') {
      this.showOutsideUser = this.helper.convertOutsideUserName([], lists, 'outside');
      for (let i = 0; i < this.showOutsideUser.length; i++) {
        for (let j = 0; j < this.listMember.length; j++) {
          if (this.showOutsideUser[i].userId === this.listMember[j].userId) {
            this.showOutsideUser.splice(i, 1);
          }
        }
      }
    } else {
      const list = [];
      this.showOutsideUser = this.helper.convertOutsideUserName([], lists, 'outside');
      for (let v = 0; v < this.showOutsideUser.length; v++) {
        if (this.showOutsideUser[v].officeId === value) {
          list.push(this.showOutsideUser[v]);
        }
      }
      this.showOutsideUser = list;
      for (let i = 0; i < this.showOutsideUser.length; i++) {
        for (let j = 0; j < this.listMember.length; j++) {
          if (this.showOutsideUser.length > 0) {
            if (this.showOutsideUser[i].userId === this.listMember[j].userId) {
              this.showOutsideUser.splice(i, 1);
            }
          }
        }
      }
    }
  }

  selectedUser(user, index, type) {
    if (type === 'inside') {
      const result = this.selectedRow(user, index, this.selectedUserRow, this.listSelectedUser);
      this.selectedUserRow = result[0];
      this.listSelectedUser = result[1];
    } else if (type === 'outside') {
      const result = this.selectedRow(user, index, this.selectedUserRowOutside, this.listSelectedUserOutside);
      this.selectedUserRowOutside = result[0];
      this.listSelectedUserOutside = result[1];
    }
  }

  selectedRow(user, index, listSelectedRow, listSelectedItem) {
    if (listSelectedRow[index]) {
      listSelectedRow[index] = false;
    } else {
      listSelectedRow[index] = true;
    }
    const arrayDup = listSelectedItem.filter((item) => item === user.userId);
    if (arrayDup.length > 0) {
      listSelectedItem = listSelectedItem.filter((i) => arrayDup.indexOf(i) === -1);
    } else {
      listSelectedItem.push(user.userId);
    }
    return [listSelectedRow, listSelectedItem];
  }

  selectedMember(user, index) {
    const result = this.selectedRow(user, index, this.selectedMemberRow, this.listSelectedMember);
    this.selectedMemberRow = result[0];
    this.listSelectedMember = result[1];
  }

  changeCheckAllCommon(event, checkAllUser, listSelectedUser, listUser, selectedUserRow) {
    if (event.target.checked) {
      checkAllUser = true;
      listSelectedUser = [];
      selectedUserRow = [];
      for (let i = 0; i < listUser.length; i++) {
        listSelectedUser.push(listUser[i].userId);
        selectedUserRow.push(true);
      }
    } else {
      checkAllUser = false;
      selectedUserRow.fill(false);
      listSelectedUser = [];
    }
    return {
      checkAllUser: checkAllUser,
      listSelectedUser: listSelectedUser,
      listUser: listUser,
      selectedUserRow: selectedUserRow
    };
  }

  changeCheckAll(event, type) {
    if (type === 'inside') {
      const result = this.changeCheckAllCommon(event, this.checkAllUser, this.listSelectedUser, this.listUser, this.selectedUserRow);
      this.checkAllUser = result.checkAllUser;
      this.listSelectedUser = result.listSelectedUser;
      this.listUser = result.listUser;
      this.selectedUserRow = result.selectedUserRow;
    } else if (type === 'outside') {
      const result = this.changeCheckAllCommon(event, this.checkAllUserOutside,
        this.listSelectedUserOutside, this.showOutsideUser, this.selectedUserRowOutside);
      this.checkAllUserOutside = result.checkAllUser;
      this.listSelectedUserOutside = result.listSelectedUser;
      this.showOutsideUser = result.listUser;
      this.selectedUserRowOutside = result.selectedUserRow;
    }
  };

  addMember(type) {
    if (type === 'inside') {
      for (let i = 0; i < this.listSelectedUser.length; i++) {
        for (let j = 0; j < this.listUser.length; j++) {
          if (this.listSelectedUser[i] === this.listUser[j].userId) {
            this.listMember.push(this.listUser[j]);
            this.selectedMemberRow.push(false);
            this.listUser.splice(j, 1);
            this.listAdmin.push(false);
            this.inviteFlg.push(false);
          }
        }
      }
      if (this.listUser.length === 0) {
        this.checkAllUser = false;
      }
      this.listSelectedUser = [];
      this.selectedUserRow = [];
    }else if (type === 'outside') {
      for (let i = 0; i < this.listSelectedUserOutside.length; i++) {
        for (let j = 0; j < this.showOutsideUser.length; j++) {
          if (this.listSelectedUserOutside[i] === this.showOutsideUser[j].userId) {
            this.listMember.push(this.showOutsideUser[j]);
            this.selectedMemberRow.push(false);
            this.showOutsideUser.splice(j, 1);
            this.listAdmin.push(false);
            this.inviteFlg.push(false);
          }
        }
      }
      if (this.showOutsideUser.length === 0) {
        this.checkAllUserOutside = false;
      }
      this.listSelectedUserOutside = [];
      this.selectedUserRowOutside = [];
    }
  }

  removeMember(type) {
    if (type === 'inside') {
      for (let i = 0; i < this.listSelectedMember.length; i++) {
        for (let j = 0; j < this.listMember.length; j++) {
          if (!this.listAdmin[j] && this.listSelectedMember[i] === this.listMember[j].userId) {
            if (this.listMember[j].back === 'inside' &&
              (this.checkBackItemInside === '' || this.listMember[j].depId === this.checkBackItemInside)) {
              this.listUser.push(this.listMember[j]);
            } else if (this.listMember[j].back === 'outside' &&
              (this.checkBackItemOutside === '' || this.listMember[j].officeId === this.checkBackItemOutside)) {
              this.showOutsideUser.push(this.listMember[j]);
            }else if (this.listMember[j].back === 'invite') {
              // remove item
            }
            this.selectedUserRow.push(false);
            this.listMember.splice(j, 1);
            this.listAdmin.splice(j, 1);
            this.inviteFlg.splice(j, 1);
          }
        }
      }
      this.listSelectedMember = [];
      this.selectedMemberRow = [];
    } else if (type === 'outside') {
      for (let i = 0; i < this.listSelectedMember.length; i++) {
        for (let j = 0; j < this.listMember.length; j++) {
          if (!this.listAdmin[j] && this.listSelectedMember[i] === this.listMember[j].userId) {
            if (this.listMember[j].back === 'inside' &&
                           (this.checkBackItemInside === '' || this.listMember[j].depId === this.checkBackItemInside)) {
              this.listUser.push(this.listMember[j]);
            } else if (this.listMember[j].back === 'outside' &&
              (this.checkBackItemOutside === '' || this.listMember[j].officeId === this.checkBackItemOutside)) {
              this.showOutsideUser.push(this.listMember[j]);
            }else if (this.listMember[j].back === 'invite') {
              // remove item
            }
            this.selectedUserRowOutside.push(false);
            this.listMember.splice(j, 1);
            this.listAdmin.splice(j, 1);
            this.inviteFlg.splice(j, 1);
          }
        }
      }
      this.checkAllUserOutside = false;
      this.listSelectedMember = [];
      this.selectedMemberRow = [];
    }
  }

  selectedHospitalAdmin(event, idx) {
    this.listAdmin = this.helper.selectedAdmin(event.target.checked, idx, this.listAdmin);
  }

  editFormOutsideGroup() {
    this.myForm = this.fb.group({
      'name': [this.model.group.name, [Validators.required, Validators.maxLength(20), Validators.pattern(Helper.checkSpace)]]
    });
    this.mailForm = this.fb.group({
      'mail': ['', Validators.required]
    });
  }

  checkValidate() {
    if (!this.myForm) {
      return false;
    }
    this.getValidationMessage();
    const form = this.myForm;

    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        this.formErrors[field] = this.helper.validateForm(form, field, this.validationMess[field]);
      }
    }
  }

  checkValidateAdmin(listAdmin, nonSelectedAdmin) {
    const arrayIsAdmin = listAdmin.filter((item) => item === true);
    if (arrayIsAdmin.length === 0) {
      nonSelectedAdmin = true;
      this.dialogService.showError('VAL.ADMIN_GROUP');
    } else {
      nonSelectedAdmin = false;
    }
    return {'nonSelectedAdmin': nonSelectedAdmin};
  }

  getValidationMessage() {
    this.translate.get('GR0006.FORM_ERROR').subscribe((res: string) => {
      this.validationForm = res;
      this.validationMess = {
        'name': {
          'required': this.validationForm.REQUIRED,
          'maxlength': this.validationForm.MAXLENGTH,
          'pattern': this.validationForm.PATTERN
        }
      };
    });
  }

  searchOfficeUser() {
    if (!this.mailForm) {
      return false;
    }else {
      if (this.mailForm.get('mail').value === '') {
        this.formErrors['mail'] = this.translate.instant('GR0006.FORM_ERROR.REQUIRED');
      }else {
        this.formErrors['mail'] = '';
      }
    }
    if (this.mailForm.valid) {
      this.mailForm.get('mail').setValue('');
      this.groupService.getUserFromMail(this.mailForm.get('mail').value).subscribe((officeUser: DepartmentUserSettings) => {
        this.officeUser = officeUser.outsideOfficeUser;
        if (this.officeUser) {
          this.checkSearch = true;
        }
      },
      (error: HttpError) => {
        if (error.contains('COMMON.SAVE_FAILED')) {

        } else {
          this.dialogService.showError('MSG.ERROR');
        }
      });
    }
  }

  addListOfficeUser() {
    const user = {
      'userId': this.officeUser.userId,
      'firstName': this.officeUser.firstName,
      'lastName': this.officeUser.lastName,
      'officeName': this.officeUser.officeName
    };
    this.checkSearch = false;
    this.listIshaOutside.push(user);
  }

  removeIsha (i: any) {
    this.listIshaOutside.splice(i, 1);
  }

  addOutsideMember() {
    for (let i = 0; i < this.listIshaOutside.length; i++) {
      const user = {
        'userId': this.listIshaOutside[i].userId,
        'firstName': this.listIshaOutside[i].firstName,
        'lastName': this.listIshaOutside[i].lastName,
        'fullName': this.listIshaOutside[i].lastName + ' ' + this.listIshaOutside[i].firstName,
        'officeName': this.listIshaOutside[i].officeName.substring(0, 8) + '...',
        'back': 'invite'
      };
      this.listMember.push(user);
      this.listIshaOutside.splice(i, 1);
      this.inviteFlg.push(true);
      this.listAdmin.push(false);
    }
  }

  putEditOutsideGroup() {
    this.checkValidate();
    const resultCheckValidation = this.checkValidateAdmin(this.listAdmin, this.nonSelectedAdmin);
    this.nonSelectedAdmin = resultCheckValidation.nonSelectedAdmin;
    let html = '';
    this.translate.get('GR0006').subscribe((res) => {
      html = '<p>' + res.CONFIRM_EDIT + '</p>' + res.CONFIRM_EDIT_1;
    });
    if (this.myForm.valid && !this.nonSelectedAdmin) {
      const dataSend = new CreateHospitalGroupSettingsSendApi();
      dataSend.name = this.myForm.get('name').value;
      dataSend.id = this.groupId;
      for (let i = 0; i < this.listMember.length; i++) {
        const mem = {};
        mem['id'] = this.listMember[i].userId;
        mem['adminFlag'] = this.listAdmin[i];
        mem['inviteFlg'] = this.inviteFlg[i];
        dataSend.users.push(mem);
      }
      this.dialogService.showMessage('warning', true, null, null, html, null, 'MSG.YES', 'MSG.NO').subscribe(
        (res: DialogResult) => {
          setTimeout(() => {
            if (res.buttonName === Helper.dialog_result_ok ) {
              this.dialogService.setLoaderVisible(true);
              this.groupService.putOutsideEditGroup(dataSend).subscribe((response: GroupOutsideSaveResult) => {
                this.dialogService.setLoaderVisible(false);
                this.checkApiResponse(response.status, 'edit', 'GR0006.EDIT_SUCCESS');
              },
              (error: HttpError) => {
                if (error.contains('COMMON.SAVE_FAILED')) {

                } else {
                  this.dialogService.setLoaderVisible(false);
                  this.dialogService.showError('MSG.ERROR');
                }
              });
            }
          }, 400);
        }
      );
    }
  }

  putDeleteOutsideGroup() {
    let html = '';
    this.translate.get('GR0006').subscribe((res) => {
      html = '<p>' + res.CONFIRM_DELETE + '</p>' + res.CONFIRM_DELETE_1;
    });
    this.dialogService.showMessage('warning', true, null, null, html, null, 'MSG.YES', 'MSG.NO').subscribe(
      (res: DialogResult) => {
        setTimeout(() => {
          if (res.buttonName === Helper.dialog_result_ok ) {
            this.dialogService.setLoaderVisible(true);
            this.groupService.deleteGroup(this.groupId).subscribe((response: GroupOutsideSaveResult) => {
              this.dialogService.setLoaderVisible(false);
              this.checkApiResponse(response.status, 'delete', 'GR0006.DELETE_SUCCESS');
            },
            (error: HttpError) => {
              if (error.contains('COMMON.SAVE_FAILED')) {

              } else {
                this.dialogService.setLoaderVisible(false);
                this.dialogService.showError('MSG.ERROR');
              }
            });
          }
        }, 400);
      }
    );
  }

  checkApiResponse(status, type, message) {
    if (status === HttpStatusCode.HTTP_PUT_SUCCESS && type === 'edit') {
      this.dialogService.showSuccess(message).subscribe(
        (res: any) => {
          // redirect to list post group
        }
      );
    } else if (status === HttpStatusCode.HTTP_PUT_SUCCESS && type === 'delete') {
      this.dialogService.showSuccess(message).subscribe(
        (res: any) => {
          // back to other page
        }
      );
    }
  }
}



