import { Component, OnInit } from '@angular/core';
import { CalendarService } from '../../../services/calendar.service';
import { SettingsExternalSettings } from '../../../models/ca/settings-external-settings';

@Component({
  selector: 'app-ca0010-page',
  templateUrl: './ca0010-page.component.html',
  styleUrls: ['./ca0010-page.component.scss']
})
export class Ca0010PageComponent implements OnInit {

  public model = new SettingsExternalSettings();

  constructor(private calendarService: CalendarService) { }

  ngOnInit() {
    this.calendarService.getSettingsExternalSettings().subscribe((settings: SettingsExternalSettings) => {
      console.log('settings = ' + JSON.stringify(settings));
      this.model = settings;
      console.log(this.model);
    });
  }

  deleteSettingsExternalSettings(settings: SettingsExternalSettings) {
    console.log(settings);
    // Edit settings-external with API

    this.calendarService.deleteSettingsExternalSettings(settings).subscribe(
      respone => {
        console.log(settings);
      }
    );
  }

}



