import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import {DialogParams} from '../../models/dialog-param';

@Component({
  selector: 'app-password-input',
  templateUrl: './password-input.component.html',
  styleUrls: ['./password-input.component.scss']
})
export class PasswordInputComponent implements OnInit {
  params: DialogParams;
  passError: boolean;
  public password: string;
  public isConfirm: boolean;
  public maxLength: number = 256;
  @Input() inputOptions: any = {
    'name': null,
    'id': null,
    'min': 0,
    'max': this.maxLength,
    'error': null
  };

  constructor() { }

  ngOnInit() {
    // TODO mock
    this.password = 'aaaaa';
  }

  onModalInit() {
    this.password = '';
    this.isConfirm = this.params.extraParams.initialValue.isConfirm;
    this.passError = this.passError;
  }

  passwordChange(value: string) {
    this.password = value;
  }

  getPayload(): any {
    return {'password': this.password, 'isConfirm': this.isConfirm};
  }

}



