import { HttpStatusCode } from '../../../common/http_status_code';
import { DialogService } from '../../../services/dialog.service';
import { Component, OnInit } from '@angular/core';
import { MeetingService } from '../../../services/meeting.service';
import { VenderBlockSettings } from '../../../models/me/vender-block-settings';
import { DialogResult } from '../../../models/dialog-param';
import { Helper } from '../../../common/helper';
import { VenderBlockSettingsSaveResult } from '../../../models/me/vender-block-settings-save-result';
import { SharedValueService } from '../../../services/shared-value.service';
import { TranslateService } from '@ngx-translate/core';
import { VenderPersonListSettings } from '../../../models/me/vender-person-list-settings';
import { VenderPersonListSettingsSaveResult } from '../../../models/me/vender-person-list-settings-save-result';

@Component({
  selector: 'app-me0012-page',
  templateUrl: './me0012-page.component.html',
  styleUrls: ['./me0012-page.component.scss']
})
export class Me0012PageComponent implements OnInit {

  public model = new VenderBlockSettings();
  public modelPIC = new VenderPersonListSettings();

  // ME0012
  firstLoad12 = true;
  // ME0013
  firstLoad = true;
  // Select2 Company
  listCompany: any;
  selectedCompany: any;
  data: Array<any>;
  options: any;
  pleaseSelect: string;
  noResult: string;
  maximumSelected: string;
  // Array User Normal and Blog
  arrUserList = [];
  arrUserListHistory = [];

  constructor(
    private meetingService: MeetingService,
    private dialogService: DialogService,
    private shareValueData: SharedValueService,
    private translate: TranslateService
  ) {
    this.listCompany = this.shareValueData.medicalOffices();
  }

  ngOnInit() {
    this.getBlockUser();
    if (this.translate.currentLang === 'en') {
      this.pleaseSelect = 'Select Company';
      this.noResult = 'No Results Found';
      this.maximumSelected = 'You can only select 1 item';

    } else {
      this.pleaseSelect = '会社を検索';
      this.noResult = '一致する会社がありません';
      this.maximumSelected = '1つのアイテムのみを選択できます';
    }
    this.options = {
      placeholder: {
        id: '0',
        text: this.pleaseSelect,
        selected: ''
      },
      language: {
        noResults: () => {
          return this.noResult;
        }
      }
    };
    const tempData = [];
    const placeholder = { id: '0', text: '' };
    tempData.push(placeholder);
    for (const child of this.listCompany) {
      const objMedicalAnother = {};
      objMedicalAnother['text'] = child.Name;
      objMedicalAnother['id'] = child.Id;
      tempData.push(objMedicalAnother);
    }
    this.data = tempData;
  }

  getBlockUser() {
    this.dialogService.setLoaderVisible(true);
    this.meetingService.getBlockUsersSettings().subscribe((settings: VenderBlockSettings) => {
      this.dialogService.setLoaderVisible(false);
      this.model = settings;
      if (this.model.users.length > 0) {
        this.firstLoad12 = false;
      } else {
        this.firstLoad12 = true;
      }
    });
  }

  unBlockUser(idUser) {
    this.dialogService.showMessage('warning', true, 'ME0012.MESSAGE_UNLOCK', null, null, null, 'MSG.YES', 'MSG.NO')
      .subscribe((res: DialogResult) => {
        if (res.buttonName === Helper.dialog_result_ok) {
          this.dialogService.setLoaderVisible(true);
          this.meetingService.postUnblockUserSettings(idUser).subscribe((response: VenderBlockSettingsSaveResult) => {
            this.dialogService.setLoaderVisible(false);
            this.dialogService.showSuccess('ME0012.UNLOCK_SUCCESS').subscribe((button: DialogResult) => {
              if (button.buttonName === Helper.dialog_result_ok) {
                this.getBlockUser();
              }
            });
          });
        }
      });
  }

  blockUser(idUser) {
    this.dialogService.showMessage('warning', true, 'ME0013.MESSAGE_TITLE_BLOCK', 'ME0013.MESSAGE_BLOCK', null, null, 'MSG.YES', 'MSG.NO')
      .subscribe((res: DialogResult) => {
        if (res.buttonName === Helper.dialog_result_ok) {
          this.dialogService.setLoaderVisible(true);
          this.meetingService.postBlockUserSettings(idUser).subscribe((response: VenderPersonListSettingsSaveResult) => {
            this.dialogService.setLoaderVisible(false);
            this.dialogService.showSuccess('ME0013.BLOCK_SUCCESS').subscribe((button: DialogResult) => {
              if (button.buttonName === Helper.dialog_result_ok) {
                this.getBlockUser();
              }
            });
          });
        }
      });
  }

  selectCompany(event) {
    this.arrUserList = [];
    this.arrUserListHistory = [];
    if (event) {
      this.firstLoad = false;
      this.getListPIC(event.id);
    } else {
      this.firstLoad = true;
    }
  }

  getListPIC(officeId) {
    this.dialogService.setLoaderVisible(true);
    this.meetingService.getPicSettings(officeId).subscribe((settings: VenderPersonListSettings) => {
      this.dialogService.setLoaderVisible(false);
      this.modelPIC = settings;
      if (!$.isEmptyObject(settings)) {
        for (const user of settings.pics) {
          if (user.blocked === 3) {
            this.arrUserListHistory.push(user);
          } else {
            this.arrUserList.push(user);
          }
        }
      }
    });
  }

}



