import { SharedValueService } from '../../../services/shared-value.service';
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';

declare const $: any;
@Component({
  selector: 'app-me0005-page',
  templateUrl: './me0005-page.component.html',
  styleUrls: ['./me0005-page.component.scss']
})
export class Me0005PageComponent implements OnInit, AfterViewInit {

  // Declare variable
  checkedMale = true;
  checkedFeMale = true;
  checkedUserPast = true;
  checkedUserBlock = false;
  selectedIndustry = [];
  selectedOffice = [];
  selectedArea = [];
  // Master Data
  listIndustry: any;
  listFieldID: any;

  constructor(
    private router: Router,
    private shareValue: SharedValueService
  ) {
    this.listIndustry = this.shareValue.getIndustryTypes();
    this.listFieldID = this.shareValue.getSpecializedDepartmentFake().fieldIds;
  }

  ngAfterViewInit() {
    $('[data-toggle="tooltip"]').tooltip();
    $('[data-toggle="popover"]').popover();
  }

  ngOnInit() {
  }

  searchUser(keyword) {
    console.log(keyword);
    console.log(this.checkedMale);
    console.log(this.checkedFeMale);
    console.log(this.checkedUserPast);
    console.log(this.checkedUserBlock);
    console.log(this.selectedIndustry);
    console.log(this.selectedOffice);
    console.log(this.selectedArea);
  }

  goToME0006(officeUserId) {
    this.router.navigate(['me/me0006', officeUserId]);
  }

  goToCH0002(officeUserId, room) {
    this.router.navigate(['me/me0006', officeUserId, room]);
  }

}



