import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SharedValueService } from './shared-value.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/forkJoin';
import {Subject} from 'rxjs/Subject';
import {DialogParams, DialogResult} from '../models/dialog-param';
import {ModalSimpleComponent} from '../components/modal-simple/modal-simple.component';
import {ModalInputComponent} from '../components/modal-input/modal-input-component';
import {ModalCheckboxComponent} from '../components/modal-checkbox/modal-checkbox.component';
import {PopupHover, PopupHoverResult} from '../models/popup-hover';
import {PasswordInputComponent} from '../components/password-input/password-input.component';

declare var swal: any;

@Injectable()
export class DialogService {
  BUTTON_COLOR = '#EF8B00';
  private showLoader: boolean;

  constructor(private translate: TranslateService, private sharedService: SharedValueService) { }

  setLoaderVisible(flag: boolean) {
    this.showLoader = flag;
  }

  isLoaderVisible() {
    return this.showLoader;
  }

  showSimpleMessage(message: string) {
    return this.showMessage('success', false, null, message, null, null, 'MSG.OK', null);
  }

  showMessage(icon: string, closeButton: boolean, title: string, message: string, html: string, under_footer: string,
     positiveButton: string, negativeButton: string) {
    return this.showModal(icon, closeButton, title, message, html, under_footer, positiveButton, negativeButton, ModalSimpleComponent, {}, null);
  }

  private showModal(icon: string, closeButton: boolean, title: string, message: string, html: string, under_footer: string,
    positiveButton: string, negativeButton: string, component: any, extraParams: any, width: string) {
    const blank = new Subject<string>();
    const subject = new Subject<DialogResult>();

    Observable.forkJoin(
      title ? this.translate.get(title).map(res => res) : blank,
      message ? this.translate.get(message).map(res => res) : blank,
      positiveButton ? this.translate.get(positiveButton).map(res => res) : blank,
      negativeButton ? this.translate.get(negativeButton).map(res => res) : blank
    ).subscribe(msgs => {
      // console.log(JSON.stringify(msgs));
      const dialog = new DialogParams(
        component,
        icon,
        closeButton,
        msgs[0],
        msgs[1],
        html,
        under_footer,
        msgs[2],
        msgs[3],
        subject,
        extraParams,
        width
      );
      this.sharedService.showModal.next(dialog);
    });
    blank.next('');
    blank.complete();
    return subject;
  }

  successMessage(message: string) {
    this.translate.get(message).subscribe(msg => {
      swal({
        type: 'success',
        text: msg,
        confirmButtonColor: this.BUTTON_COLOR,
        allowOutsideClick: false
      });
    });
  }

  showSuccess(message: string) {
    return this.showMessage('success', false, null, message, null, null, 'MSG.OK', null);
  }

  showError(message: string)  {
    return this.showMessage('error', false, null, message, null, null, 'MSG.OK', null);
  }

  showInputDialog(message: string, initialValue: string) {
    return this.showModal(null, false, null, message, '', '', 'MSG.OK', 'MSG.CANCEL', ModalInputComponent, { initialValue: initialValue }, null);
  }

  showCheckboxDialog(icon: string, closeButton: boolean, title: string, message: string, html: string, positiveButton: string,
                       negativeButton: string, object: any) {
    return this.showModal(icon, closeButton, title, message, html, null, positiveButton, negativeButton, ModalCheckboxComponent,
      {initialValue: object}, null);
  }

  showInputPasswordDialog(html: string, object: any) {
    return this.showModal('warning', false, null, null, html, null, 'MSG.OK', 'MSG.CANCEL', PasswordInputComponent, { initialValue: object }, null);
  }

  showCheckboxDialogChangeWidth(icon: string, closeButton: boolean, title: string, message: string, html: string, positiveButton: string,
                        negativeButton: string, object: any, width: string) {
    return this.showModal(icon, closeButton, title, message, html, null, positiveButton, negativeButton, ModalCheckboxComponent,
      {initialValue: object}, width);
  }

  showPopupHoverDialog(title: string, time: string, user: string, note: string, place: string, jsEvent: any,
                       holiday: boolean, bntShow: boolean) {
    const subject = new Subject<PopupHoverResult>();
    const blank = new Subject<string>();

    Observable.forkJoin(
      title ? this.translate.get(title).map(res => res) : blank,
    ).subscribe(msgs => {
      const popupHover = new PopupHover(msgs[0], time, user, note, place, jsEvent, holiday, bntShow, subject);

      this.sharedService.showPopupHover.next(popupHover);
    });
    blank.next('');
    blank.complete();
    return subject;
  }

  showEditPopupHoverDialog(title: string, time: string, user: string, note: string, place: string, jsEvent: any, bntShow) {
    return this.showPopupHoverDialog(title, time, user, note, place, jsEvent, false, bntShow);
  }

  showHolidayPopupHoverDialog(title: string, time: string, jsEvent: any) {
    return this.showPopupHoverDialog(title, time, null, null, null, jsEvent, true, false);
  }
}



