import { Component, OnInit } from '@angular/core';
import { RegistrationService } from '../../../services/registration.service';
import { PrFirstEntrySettings } from '../../../models/re/pr-first-entry-settings';
import { FormControl, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Helper } from '../../../common/helper';
import { SharedValueService } from '../../../services/shared-value.service';
import { DialogService } from '../../../services/dialog.service';
import { DrJOY } from '../../../models/ba/drjoy';
import { PrFirstEntrySettingsSaveResult } from '../../../models/re/pr-first-entry-settings-save-result';
import { DialogResult } from '../../../models/dialog-param';
import { Re0006PageValidator } from './re0006-page.validator';

declare const $: any;
declare const swal: any;
@Component({
  selector: 'app-re0006-page',
  templateUrl: './re0006-page.component.html',
  styleUrls: ['./re0006-page.component.scss'],
  providers: [Re0006PageValidator]
})
export class Re0006PageComponent implements OnInit {
  selectCheck = true;
  modelGroupForm: FormGroup;
  formErrors: any;
  model: any;
  submitForm = false;
  registerToken: string;
  emailSupport: String;
  infoRegister = new PrFirstEntrySettings();
  checkRadio = true;
  selectedIndustry = '';
  pleaseSelect: string;
  noResult: string;

  // Information
  genders: any;
  monthYears: any;
  dayOfMonths: any;
  options_birthday_year = [];
  options_birthday_text = [];
  birthday_year_text;
  combineBirth: string;
  combinePhoneNo: string;
  combineMobileNo: string;

  // Show Exp Group
  show_exp_group = false;
  show_exp = false;
  show_grade = false;
  show_area = false;

  // Show hide input form, confirm form
  register_input = true;
  register_confirm = false;
  register_complete = false;
  medicalOffices: any;
  yearsExpertise: any;
  majorAreas: any;
  listKeyProvince = [];
  listIndustryTypes = [];
  industryTypeCompany = 'I0001';

  // Select 2
  validateselectedMedical = false;
  validateSpecializedDepartment = false;
  selectedMedical: any;
  selectedDefault: string;
  selectMedicalName: string;
  maximumSelected: string;
  data = [];
  options: any;
  checkStatus: boolean;
  medicalPhone;

  constructor(
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private translate: TranslateService,
    private registrationService: RegistrationService,
    private sharedValueService: SharedValueService,
    private dialogService: DialogService,
    private helper: Helper,
    private router: Router,
    private re0006Validator: Re0006PageValidator
  ) {
    this.formErrors = this.re0006Validator.formErrors;
    this.modelGroupForm = this.re0006Validator.re0006PageForm;
    this.medicalOffices = this.sharedValueService.medicalOffices();
    this.yearsExpertise = this.sharedValueService.yearsExpertise();
    this.majorAreas = this.sharedValueService.majorAreas();
    this.dayOfMonths = this.helper.dayOfMonths;
    this.monthYears = this.helper.monthYears;
    this.genders = this.helper.genders;
  }

  ngOnInit() {
    $('tr.disable-row').click(false);
    if (this.translate.currentLang === 'en') {
      this.pleaseSelect = 'Select Medical';
      this.noResult = 'No Results Found';
      this.maximumSelected = 'You can only select 1 item';
    } else {
      this.pleaseSelect = '会社名の一部を入力';
      this.noResult = '一致する会社がありません';
      this.maximumSelected = '1つのアイテムのみを選択できます';
    }
    this.options = {
      multiple: true,
      maximumSelectionLength: 1,
      placeholder: this.pleaseSelect,
      language: {
        noResults: () => {
          return this.noResult;
        },
        maximumSelected: () => {
          return this.maximumSelected;
        }
      }
    };
    this.birthdayYearInit();
    this.activatedRoute.params.subscribe((param: any) => {
      this.registerToken = param['registerToken'];
    });

    this.sharedValueService.getDrJOY().subscribe(
      (res: DrJOY) => {
        this.emailSupport = res.supportEmail;
      }, error => {
        this.emailSupport = '';
      });
    this.listKeyProvince = this.sharedValueService.getProvinceFake();
    this.listIndustryTypes = this.sharedValueService.getIndustryTypes();
    this.dialogService.setLoaderVisible(true);
    this.registrationService.getPrFirstEntrySettings(this.registerToken).subscribe(
      (res: PrFirstEntrySettings) => {
        this.infoRegister = res;
        this.selectMedicalName = res.medicalOfficeName;
        this.selectedDefault = res.medicalOfficeId;
        this.selectedMedical = res.medicalOfficeId;
        this.medicalPhone = res.contactPhoneNo;
        if (this.selectedMedical) {
          this.selectMedical(this.selectedMedical);
        }
        this.dialogService.setLoaderVisible(false);
      }, error => {
        this.dialogService.setLoaderVisible(false);
      }
    );
    this.selectIndustry(this.selectedIndustry);

    // Select 2
    for (const child of this.medicalOffices) {
      const objMedicalAnother = {};
      objMedicalAnother['text'] = child.Name;
      objMedicalAnother['id'] = child.Id;
      this.data.push(objMedicalAnother);
    }
  }

  birthdayYearInit() {
    const value = this.helper.parseBirthDate('');
    this.monthYears = value.months;
    this.dayOfMonths = value.days;
    if (this.dayOfMonths) {
      for (let i = 1; i <= 31; i++) {
        const d = (i < 10 ? '0' : '') + i;
        this.dayOfMonths.push(d);
      }
    }

    const year = this.helper.graduationYearOptions(88);
    this.options_birthday_year = year[0];
    this.options_birthday_text = year[1];
    const graduation_year = '2017';
    this.birthday_year_text = this.helper.graduationYearText(graduation_year);
  }

  acceptNumber(event) {
    this.helper.numericOnly(event);
  }

  changeSpecializedDepartment(index) {
    const arrayExpert = this.modelGroupForm.controls.specializedDepartment.value;
    const specializedDepartmentChange = arrayExpert[index];

    let check = true;
    for (let i = 0; i < arrayExpert.length; i++) {
      if (i !== index) {
        if (arrayExpert[i].fieldId === arrayExpert[index].fieldId) {
          check = false;
          break;
        }
      }
    }
    if (!check) {
      this.dialogService.showMessage('warning', false, 'RE0006.SWAL.EXIST_DEPARTMENT', null, null, null, 'MSG.YES', null).subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            $('.reset_' + index).val('');
          }
        });
    }
  }

  combineBirthDay() {
    this.combineBirth = this.modelGroupForm.value.birthDayYear + '-' +
      this.modelGroupForm.value.birthDayMonth + '-' +
      this.modelGroupForm.value.birthDayDay;
  }

  combinebranchPhone() {
    this.combinePhoneNo = this.modelGroupForm.value.phoneOffice1
      + this.modelGroupForm.value.phoneOffice2 + this.modelGroupForm.value.phoneOffice3;
  }

  combineMobilePhone() {
    this.combineMobileNo = this.modelGroupForm.value.phone1
      + this.modelGroupForm.value.phone2 + this.modelGroupForm.value.phone3;
  }

  postPrFirstEntrySettings() {
    this.combineBirthDay();
    this.combinebranchPhone();
    this.combineMobilePhone();
    this.model = this.modelGroupForm.value;
    this.model.branchPhoneNo = this.combinePhoneNo;
    this.model.mobileNo = this.combineMobileNo;
    this.model.birthDate = this.combineBirth;
    this.model.identificationFileName = 'identificationFileName';
    this.model.identificationImageUrl = 'http://abc.com/upload/avata.png';
    this.model.identificationUpdated = '11/01/1991';
    if (this.selectedMedical.length > 0) {
      this.validateselectedMedical = false;
      if (typeof (this.selectedMedical) === 'string') {
        this.model.medicalOfficeId = this.selectedMedical;
      } else if (typeof (this.selectedMedical) === 'object') {
        this.model.medicalOfficeId = this.selectedMedical[0].id;
      }
    } else {
      this.validateselectedMedical = true;
    }
    console.log(this.modelGroupForm.value);
    this.re0006Validator.checkValidate();
    this.re0006Validator.checkSpaceBlank(this.modelGroupForm);
    if (this.validateselectedMedical) {
      $('.select2-selection--multiple').css('border-color', '#D21D29');
    } else {
      $('.select2-selection--multiple').css('border-color', '#AAA');
    }
    const $file = $('.image-cropper-upload');
    const $image = $('.image-cropper-picture');
    const origindata = $file.val();
    if (origindata) {
      const cropcanvas = $image.cropper('getCroppedCanvas');
      const croppng = cropcanvas.toDataURL('image/png');
      console.log(croppng);
    }
    if (this.modelGroupForm.value.industryType === this.industryTypeCompany) {
      if (this.modelGroupForm.value.specializedDepartment[0].fieldId === '') {
        this.validateSpecializedDepartment = true;
      } else {
        this.validateSpecializedDepartment = false;
      }
    }
    if (this.model.medicalOfficeId !== this.selectedDefault && this.selectedDefault !== '') {
      this.dialogService.showMessage('warning', false, 'RE0006.SWAL.REPLACE_COMPANY', null, null, null, 'OK', null).subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.selectedMedical = this.selectedDefault;
          }
        });
    } else {
      if (this.modelGroupForm.valid && !this.formErrors.access && this.formErrors['confirmPassword'] === ''
        && this.validateSpecializedDepartment === false) {
        this.register_input = false;
        this.register_confirm = true;
        this.register_complete = false;
      }
    }
  }

  addSpecializedDepartment() {
    if (this.modelGroupForm.get('specializedDepartment').value.length < 5) {
      const specializedDepartment = <FormArray>this.modelGroupForm.get('specializedDepartment');
      const newSpecializedDepartment = this.fb.group({ fieldId: ['', [Validators.required]], typeId: '' });
      specializedDepartment.push(newSpecializedDepartment);
    }
  }

  deleteSpecializedDepartment(index) {
    const specializedDepartment = <FormArray>this.modelGroupForm.get('specializedDepartment');
    specializedDepartment.removeAt(index);
  }

  selectIndustry(selectData) {
    if (selectData === this.industryTypeCompany) {
      this.show_exp_group = true;
      this.show_exp = true;
      this.show_grade = true;
      this.show_area = true;
    } else if (selectData === '') {
      this.show_exp_group = false;
      this.show_exp = false;
    } else {
      this.show_exp_group = true;
      this.show_exp = true;
      this.show_grade = false;
      this.show_area = false;
    }
  }

  registerComplete() {
    this.dialogService.setLoaderVisible(true);
    this.registrationService.postPrFirstEntrySettings(this.model).subscribe(
      (response: PrFirstEntrySettingsSaveResult) => {
        this.dialogService.setLoaderVisible(false);
        this.register_input = false;
        this.register_confirm = false;
        this.register_complete = true;
        console.log(this.model);
      }, error => {
        this.dialogService.setLoaderVisible(false);
        this.translate.get('MSG.ERROR').subscribe(msg => {
          swal({
            type: 'error',
            text: msg,
            confirmButtonColor: '#EF8B00',
            allowOutsideClick: false
          }).then(() => {
            location.reload();
          });
        });
      }
    );
  }

  registerBackInput() {
    this.register_input = true;
    this.register_confirm = false;
    this.register_complete = false;
  }

  selectMedical(event) {
    if (!$.isEmptyObject(event)) {
      this.selectMedicalName = event[0].text;
    }
  }

  changeDate() {
    const value = this.helper.changeDate('year', 'month');
    this.monthYears = value.months;
    this.dayOfMonths = value.days;
    const chooseDay = $('#day').val();
    const chooseMonth = $('#month').val();
    if (this.dayOfMonths.indexOf(chooseDay) === -1) {
      $('#day').val('');
      this.modelGroupForm.controls.birthDayDay.setValue('');
    }
  }
}



