/**
 * RE0015 スタッフ一括招待
 */
export class StaffInvite {
  public firstName: string;
  public lastName: string;
  public lastNameKana: string;
  public firstNameKana: string;
  public department: any;
  public jobType: string;
  public email: string;
  public managementAuthority: string;
  public functionAuthoritySet: string;

  constructor(firstName, lastName, lastNameKana, firstNameKana, department, jobType, email, managementAuthority, functionAuthoritySet) {
    this.firstName = firstName;
    this.lastName = lastName;
    this.lastNameKana = lastNameKana;
    this.firstNameKana = firstNameKana;
    this.department = department;
    this.jobType = jobType;
    this.email = email;
    this.managementAuthority = managementAuthority;
    this.functionAuthoritySet = functionAuthoritySet;
  }
}


