import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {DialogService} from '../../../services/dialog.service';
import {GroupService} from '../../../services/group.service';
import {SharedValueService} from '../../../services/shared-value.service';
import {Helper} from '../../../common/helper';
import {EditDepartmentGroupSettings} from '../../../models/gr/edit-department-group-settings';
import {ActivatedRoute, Params} from '@angular/router';
import {Gr0003PageComponent} from '../gr0003-page/gr0003-page.component';
import {DialogResult} from '../../../models/dialog-param';
import {Gr0004PageComponent} from '../gr0004-page/gr0004-page.component';
import {EditDepartmentGroupSettingsSendApi} from '../../../models/gr/edit-department-group-settings-send-api';

@Component({
  selector: 'app-gr0005-page',
  templateUrl: './gr0005-page.component.html',
  styleUrls: ['./gr0005-page.component.scss'],
})
export class Gr0005PageComponent implements OnInit {

  public editDepartmentModel = new EditDepartmentGroupSettings();
  editInfoForm: FormGroup;
  gr0003;
  gr0004;
  groupId = '';
  listMember = [];
  listDepartment = [];
  formErrors = {
    'name': '',
    'deptId': ''
  };
  nonSelectedAdmin = false;
  userSession;
  department;

  constructor(private translate: TranslateService,
              private fb: FormBuilder,
              private dialogService: DialogService,
              private groupService: GroupService,
              private helper: Helper,
              private sharedValueService: SharedValueService,
              private activatedRoute: ActivatedRoute) {
    this.editInfoForm = this.fb.group({
      'name': ['', Validators.compose([Validators.required, Validators.maxLength(20), Validators.pattern(Helper.regexGroupName)])],
      'deptId': ['', Validators.required]
    });
    this.gr0003 = new Gr0003PageComponent(this.translate, this.fb, this.dialogService, this.groupService,
      this.helper, this.sharedValueService, null);
    this.gr0004 = new Gr0004PageComponent(this.translate, this.dialogService, this.fb, this.groupService, this.helper,
      this.sharedValueService, this.activatedRoute);
  }


  ngOnInit() {
    this.dialogService.setLoaderVisible(true);
    this.userSession = this.sharedValueService.fetchUserSession();
    this.activatedRoute.params.subscribe((params: Params) => {
      this.groupId = params['groupId'];
    });
    this.groupService.getEditDepartmentGroup(this.groupId).subscribe(
      (settings: EditDepartmentGroupSettings) => {
        this.dialogService.setLoaderVisible(false);
        this.editDepartmentModel = settings;
        this.createForm();
      },
      error => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('MSG.ERROR');
      }
    );
  }

  createForm() {
    this.editInfoForm = this.fb.group({
      'name': [this.editDepartmentModel.group.name,
        Validators.compose([Validators.required, Validators.maxLength(20), Validators.pattern(Helper.regexGroupName)])],
      'deptId': this.editDepartmentModel.group.deptName
    });
    this.listMember = this.helper.convertUserName(this.listMember, this.editDepartmentModel.group.users);
    this.listMember.sort(this.gr0004.orderByComparator('authority'));
  }

  editSelectedDepartmentAdmin(event, idx) {
    if (event.target.checked) {
      this.listMember[idx].authority = 'ADMIN';
    } else {
      this.listMember[idx].authority = 'MEMBER';
    }
  }

  deleteDepartmentGroup(groupId: string) {
    this.gr0004.deleteHospitalGroup(groupId);
  }

  putEditDepartmentGroupForm() {
    const resultCheckValidation =
      this.gr0003.checkValidate(this.editInfoForm, this.formErrors, this.listMember, [] , this.nonSelectedAdmin);
    this.formErrors = resultCheckValidation.formErrors;
    this.nonSelectedAdmin = resultCheckValidation.nonSelectedAdmin;
    if (this.editInfoForm.valid && !this.nonSelectedAdmin) {
      this.dialogService.setLoaderVisible(true);
      const modelSendApi = new EditDepartmentGroupSettingsSendApi();
      modelSendApi.name = this.editInfoForm.get('name').value;
      modelSendApi.imageFlg = true;
      modelSendApi.id = this.editDepartmentModel.group.id;
      for (let i = 0; i < this.listMember.length; i++) {
        const mem = {};
        mem['id'] = this.listMember[i].userId;
        if (this.listMember[i].authority === 'ADMIN') {
          mem['adminFlg'] = true;
        } else {
          mem['adminFlg'] = false;
        }
        modelSendApi.users.push(mem);
      }
      this.groupService.putEditDepartmentGroup(modelSendApi).subscribe(
        (response) => {
          this.dialogService.setLoaderVisible(false);
          this.dialogService.showMessage('success', false, null, 'MSG.GR.M009', null, null, 'MSG.YES', null).subscribe(
            (res: DialogResult) => {
              if (res.buttonName === Helper.dialog_result_ok) {
                // redirect to group's timeline page
              }
            }
          );
        }, error => {
          this.dialogService.setLoaderVisible(false);
          this.dialogService.showError('MSG.ERROR');
        }
      );
    }
  }
}



