import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {SharedValueService} from '../../../services/shared-value.service';
import {Helper} from '../../../common/helper';
import {DialogService} from '../../../services/dialog.service';
import {ActivatedRoute, Params} from '@angular/router';
import {EditHospitalGroupSettings} from '../../../models/gr/edit-hospital-group-settings';
import {GroupService} from '../../../services/group.service';
import {Gr0002PageComponent} from '../gr0002-page/gr0002-page.component';
import {DialogResult} from '../../../models/dialog-param';
import {CreateHospitalGroupSettingsSendApi} from '../../../models/gr/create-hospital-group-settings-send-api';

@Component({
  selector: 'app-gr0004-page',
  templateUrl: './gr0004-page.component.html',
  styleUrls: ['./gr0004-page.component.scss']
})
export class Gr0004PageComponent implements OnInit {

  public editHospitalModel = new EditHospitalGroupSettings();
  editGroupForm: FormGroup;
  userSession;
  groupId = '';
  groupNameError = '';
  gr0002;
  listMember = [];
  listUser = [];
  listDepartment = [];
  checkAllUser = false;
  listSelectedUser = [];
  selectedUserRows = [];
  listSelectedMember = [];
  selectedMemberRow = [];
  nonSelectedAdmin = false;

  constructor(private translate: TranslateService,
              private dialogService: DialogService,
              private fb: FormBuilder,
              private groupService: GroupService,
              private helper: Helper,
              private sharedValueService: SharedValueService,
              private activatedRoute: ActivatedRoute) {
    this.editGroupForm = this.fb.group({
      'name': '',
      'deptId': ''
    });
    this.gr0002 = new Gr0002PageComponent(this.translate, this.dialogService, this.fb, this.groupService,
      this.helper, this.sharedValueService, null);
  }

  ngOnInit() {
    this.dialogService.setLoaderVisible(true);
    this.userSession = this.sharedValueService.fetchUserSession();
    this.activatedRoute.params.subscribe((params: Params) => {
      this.groupId = params['groupId'];
    });
    if (this.groupId) {
      this.groupService.getEditHospitalGroup(this.groupId).subscribe((settings: EditHospitalGroupSettings) => {
        this.dialogService.setLoaderVisible(false);
        this.editHospitalModel = settings;
        this.createForm();
      },
      error => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('COMMON_ERROR');
      });
    }
  }

  createForm() {
    this.editGroupForm = this.fb.group({
      'name': [this.editHospitalModel.group.name,
        Validators.compose([Validators.required, Validators.maxLength(20), Validators.pattern(Helper.regexGroupName)])],
      'deptId': ''
    });
    this.listDepartment = this.helper.createArrayDepartment(this.listDepartment, this.editHospitalModel.departments, '', 1);
    this.listUser = this.helper.convertUserName(this.listUser, this.editHospitalModel.departmentUsers);
    for (let m = 0; m < this.editHospitalModel.group.users.length; m++) {
      for (let n = 0; n < this.listUser.length; n++) {
        if (this.editHospitalModel.group.users[m].userId === this.listUser[n].userId) {
          const mem = {};
          mem['userId'] = this.listUser[n].userId;
          mem['deptId'] = this.listUser[n].deptId;
          mem['authority'] = this.editHospitalModel.group.users[m].authority;
          mem['fullName'] = this.listUser[n].fullName;
          mem['fullNameKana'] = this.listUser[n].fullNameKana;
          this.listMember.push(mem);
          this.listUser.splice(n, 1);
        }
      }
    }
    for (let i = 0; i < this.listUser.length; i++) {
      this.selectedUserRows.push(false);
    }
    this.listMember.sort(this.orderByComparator('authority'));
  }

  orderByComparator(property: string) {
    return ((a: any, b: any) => {
      if ((isNaN(parseFloat(a[property])) || !isFinite(a[property])) || (isNaN(parseFloat(b[property])) || !isFinite(b[property]))) {
        if (a[property].toLowerCase() < b[property].toLowerCase()) {
          return -1;
        }
        if (a[property].toLowerCase() > b[property].toLowerCase()) {
          return 1;
        }
      } else {
        if (parseFloat(a[property]) < parseFloat(b[property])) {
          return -1;
        }
        if (parseFloat(a[property]) > parseFloat(b[property])) {
          return 1;
        }
      }
      return 0;
    });
  }

  editSelectedHospitalAdmin(event, idx) {
    if (event.target.checked) {
      this.listMember[idx].authority = 'ADMIN';
    } else {
      this.listMember[idx].authority = 'MEMBER';
    }
  }

   changeSeletedDepartment(deptId) {
    const result = this.gr0002.getDepartmentUserInHospitalGroup(deptId, this.editHospitalModel.departmentUsers, this.listMember);
    this.listUser = result.listUser;
    this.selectedUserRows = result.selectedUserRow;
  }

  editSelectedUserRow(user, index) {
    const result = this.gr0002.selectedRow(user, index, this.selectedUserRows, this.listSelectedUser);
    this.selectedUserRows = result[0];
    this.listSelectedUser = result[1];
  }

  editSelectedMember(user, index) {
    const result = this.gr0002.selectedRow(user, index, this.selectedMemberRow, this.listSelectedMember);
    this.selectedMemberRow = result[0];
    this.listSelectedMember = result[1];
  }

  changeCheckAllUser(event) {
    const result = this.gr0002.changeCheckAllCommon(event, this.checkAllUser, this.listSelectedUser, this.listUser, this.selectedUserRows);
    this.checkAllUser = result.checkAllUser;
    this.listSelectedUser = result.listSelectedUser;
    this.listUser = result.listUser;
    this.selectedUserRows = result.selectedUserRow;
  };

  editAddMember() {
    for (let i = 0; i < this.listSelectedUser.length; i++) {
      for (let j = 0; j < this.listUser.length; j++) {
        if (this.listSelectedUser[i] === this.listUser[j].userId) {
          const mem = {};
          mem['userId'] = this.listUser[j].userId;
          mem['deptId'] = this.listUser[j].deptId;
          mem['authority'] = 'MEMBER';
          mem['fullName'] = this.listUser[j].fullName;
          mem['fullNameKana'] = this.listUser[j].fullNameKana;
          this.listMember.push(mem);
          this.selectedMemberRow.push(false);
          this.listUser.splice(j, 1);
        }
      }
    }
    this.checkAllUser = false;
    this.listSelectedUser = [];
    this.selectedUserRows = [];
  }

  editRemoveMember() {
    const selectedDeptId = this.editGroupForm.get('deptId').value;
    for (let i = 0; i < this.listSelectedMember.length; i++) {
      for (let j = 0; j < this.listMember.length; j++) {
        if (this.listMember[j].authority !== 'ADMIN' && this.listSelectedMember[i] === this.listMember[j].userId) {
          if (selectedDeptId === '' || this.listMember[j].deptId === selectedDeptId) {
            const user = {};
            user['userId'] = this.listMember[j].userId;
            user['deptId'] = this.listMember[j].deptId;
            user['fullName'] = this.listMember[j].fullName;
            user['fullNameKana'] = this.listMember[j].fullNameKana;
            this.listUser.push(user);
          }
          this.selectedUserRows.push(false);
          this.listMember.splice(j, 1);
        }
      }
    }
    this.checkAllUser = false;
    this.listSelectedMember = [];
    this.selectedMemberRow = [];
  }

  deleteHospitalGroup(groupId: string) {
    let html = '';
    this.translate.get('MSG.GR').subscribe((msg) => {
      html = '<div>' + msg.M006_1 + '</div><div>' + msg.M006_2 + '</div>';
    });
    this.dialogService.showMessage('warning', false, null, null, html, null, 'MSG.YES', 'MSG.NO').subscribe(
      (res: DialogResult) => {
        if (res.buttonName === Helper.dialog_result_ok) {
          this.dialogService.setLoaderVisible(true);
          this.groupService.deleteGroup(groupId).subscribe((response) => {
            this.dialogService.setLoaderVisible(false);
            setTimeout(() => {
              this.dialogService.showMessage('success', false, null, 'MSG.GR.M007', null, null, 'MSG.YES', null).subscribe(
                (re: DialogResult) => {
                  if (re.buttonName === Helper.dialog_result_ok) {
                    // redirect to .....
                  }
                });
            });
          }, error => {
            this.dialogService.setLoaderVisible(false);
            this.dialogService.showError('MSG.ERROR');
          });
        }
      });
  }

  putEditHospitalGroup() {
    const resultCheckValidation = this.gr0002.checkValidate(this.editGroupForm, this.groupNameError,
      this.listMember, [], this.nonSelectedAdmin);
    this.groupNameError = resultCheckValidation.msgError;
    this.nonSelectedAdmin = resultCheckValidation.nonSelectedAdmin;
    if (this.editGroupForm.valid && !this.nonSelectedAdmin) {
      this.dialogService.setLoaderVisible(true);
      const modelSendApi = new CreateHospitalGroupSettingsSendApi();
      modelSendApi.name = this.editGroupForm.get('name').value;
      modelSendApi.imageFlg = true;
      for (let i = 0; i < this.listMember.length; i++) {
        const mem = {};
        mem['id'] = this.listMember[i].userId;
        mem['adminFlg'] = (this.listMember[i].authority === 'ADMIN') ? true : false;
        modelSendApi.users.push(mem);
      }
      this.groupService.putEditHospitalGroup(modelSendApi).subscribe(
        (response) => {
          this.dialogService.setLoaderVisible(false);
          this.dialogService.showMessage('success', false, null, 'MSG.GR.M009', null, null, 'MSG.YES', null).subscribe(
            (res: DialogResult) => {
              if (res.buttonName === Helper.dialog_result_ok) {
                // redirect to group's timeline page
              }
            }
          );
        }, error => {
          this.dialogService.setLoaderVisible(false);
          this.dialogService.showError('MSG.ERROR');
        }
      );
    }
  }
}



