import { Pipe, PipeTransform } from '@angular/core';
import { CalendarCommon } from '../calendar-common';
declare var moment: any;

@Pipe({ name: 'HousDay' })
// date format 11月4日(土)
export class HousDay implements PipeTransform {

    constructor(private calendarCommon: CalendarCommon) { }
    transform(value: string): string {
        const dt = this.calendarCommon.convertDatetime(value);
        const dateTime = dt.time;
        return dateTime;
    }
}


