import {Component, OnInit} from '@angular/core';

declare const $: any;

@Component({
  selector: 'app-info-panel',
  templateUrl: './info-panel.component.html',
  styleUrls: ['./info-panel.component.scss']
})
export class InfoPanelComponent implements OnInit {

  constructor() {
  }

  ngOnInit() {

    const $contentWrap = $('.content-wrap'),
      $sideMenu = $('.side-menu'),
      $navbarCollapse = $('.navbar-collapse'),
      $navbarBtnSideMenuToggle = $('.navbar-btn-side-menu-toggle'),
      $navbarBtnSideMenuToggleFa = $navbarBtnSideMenuToggle.find('.fa'),
      $navbarBtnInfoPanelToggle = $('.navbar-btn-info-panel-toggle'),
      $navbarBtnInfoPanelToggleFa = $navbarBtnInfoPanelToggle.find('.fa'),
      $infoPanel = $('.info-panel');

    $infoPanel.on('shown.bs.collapse', function () {
      const windowWidth = $(window).width() < 767;
      if (windowWidth) {
        $('html,body').css('overflow', 'hidden');
      }
      $contentWrap.removeClass('push').addClass('pull');
      $sideMenu.removeClass('side-menu-show').addClass('side-menu-hide');
      $navbarCollapse.removeClass('show');
      $navbarBtnSideMenuToggleFa.removeClass('fa-toggle-on').addClass('fa-toggle-off');
      $navbarBtnInfoPanelToggle.css('color', 'rgba(255,255,255,.75)');
      $navbarBtnInfoPanelToggleFa.removeClass('fa-send').addClass('fa-send-o');
    }).on('hide.bs.collapse', function () {
      $contentWrap.removeClass('pull');
    }).on('hidden.bs.collapse', function () {
      if ($sideMenu.hasClass('side-menu-show')) {
        $('html,body').css('overflow', 'hidden');
      } else {
        $('html,body').css('overflow', 'auto');
      }
      $navbarBtnInfoPanelToggle.css('color', '');
      $navbarBtnInfoPanelToggleFa.removeClass('fa-send-o').addClass('fa-send');
    });

    $contentWrap.click(function () {
      $infoPanel.collapse('hide');
    });

  }

}



