import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { Validator } from '../../../common/validation/validator';
import { StaffInvite } from '../../../models/re/staff-invite';
import { Helper } from '../../../common/helper';

@Injectable()
export class StaffInvitePageValidator {
  formBuilder: any;
  validationMessages: any;
  jobDoctor = 'J0001';
  jobPharmacist = 'J0002';
  functionAuthoritySet = 'FPS_4';
  checkVaild = true;


  constructor(private fb: FormBuilder, private translate: TranslateService, private validator: Validator) {
    this.getErrorValidate();
  }

  /*
   * create form change password
   */
  createStaff(model: StaffInvite) {
    let form: FormGroup;
    form = this.fb.group({
      'firstName': [model.firstName, [Validators.required, Validators.maxLength(8)]],
      'lastName': [model.lastName, [Validators.required, Validators.maxLength(8)]],
      'firstNameKana': [model.firstNameKana, [Validators.required, Validators.maxLength(8), Validators.pattern(Validator.nameKanaRegex)]],
      'lastNameKana': [model.lastNameKana, [Validators.required, Validators.maxLength(8), Validators.pattern(Validator.nameKanaRegex)]],
      'jobType': [model.jobType, [Validators.required]],
      'department': [model.department, [Validators.required]],
      'email': [model.email, [Validators.minLength(8), Validators.maxLength(64)]],
      'managementAuthority': [model.managementAuthority, [Validators.required]],
      'functionAuthoritySet': [model.functionAuthoritySet, [Validators.required]]
    });

    return form;
  }

  checkValidate(index: number, fieldForm: string, form: FormGroup, arrayErrorForm: any) {
    this.getErrorValidate();
    this.checkVaild = true;
    const listStaff = form.get('staff') as FormArray;
    if (fieldForm) {
      arrayErrorForm[index][fieldForm] = this.validator.validateForm(<FormGroup>listStaff.controls[index], fieldForm,
        this.validationMessages[fieldForm]);
    } else {
      for (const field in arrayErrorForm[index]) {
        if (arrayErrorForm[index].hasOwnProperty(field)) {
          arrayErrorForm[index][field] = this.validator.validateForm(<FormGroup>listStaff.controls[index], field,
            this.validationMessages[field]);
        } else {
          // do nothing
        }
      }
      const jobType = listStaff.controls[index].get('jobType').value;
      const functionAu = listStaff.controls[index].get('functionAuthoritySet').value;
      if (jobType === this.jobDoctor && functionAu === this.functionAuthoritySet || jobType === this.jobPharmacist &&
        functionAu === this.functionAuthoritySet) {
        arrayErrorForm[index]['functionAuthoritySet'] = this.validationMessages['functionAuthoritySet']['matchAuthority'];
        this.checkVaild = false;
      }

    }
    const email = listStaff.controls[index].get('email').value;
    if (email !== '' && email !== null) {
      if (email.length >= 8 && email.length <= 64) {
        if (email.indexOf('@') !== -1) {
          if (Helper.emailRegex.test(email) === false) {
            if (Helper.loginIDRegex.test(email) === false) {
              arrayErrorForm[index]['email'] = this.validationMessages['email']['pattern1'];
            } else {
              arrayErrorForm[index]['email'] = '';
            }
          } else {
            arrayErrorForm[index]['email'] = '';
          }
        } else {
          if (Helper.loginIDRegex.test(email) === false) {
            arrayErrorForm[index]['email'] = this.validationMessages['email']['pattern1'];
            this.checkVaild = false;
          } else {
            arrayErrorForm[index]['email'] = '';
          }
        }
      } else {
        arrayErrorForm[index]['email'] = this.validationMessages['email'].minlength;
      }
    }
    const depat = listStaff.controls[index].get('department').value;
    if (!depat || depat.id === '' || depat === null) {
      arrayErrorForm[index]['department'] = this.validationMessages['department'].required;
      this.checkVaild = false;
    } else {
      arrayErrorForm[index]['email'] = '';
    }
    return this.checkVaild;
  }

  getErrorValidate() {
    this.translate.get(['RE0015', 'VAL']).subscribe((res: any) => {
      this.validationMessages = {
        'firstName': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED,
          'maxlength': res.VAL.FIRST_NAME
        },
        'lastName': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED,
          'maxlength': res.VAL.LAST_NAME
        },
        'lastNameKana': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED,
          'pattern': res.VAL.FIRST_NAME_KANA,
          'maxlength': res.VAL.FIRST_NAME_KANA
        },
        'firstNameKana': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED,
          'pattern': res.VAL.LAST_NAME_KANA,
          'maxlength': res.VAL.LAST_NAME_KANA
        },
        'department': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED
        },
        'jobType': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED
        },
        'email': {
          'minlength': res.RE0015.MSG_VALIDATION.MAX_LENGHT_EMAIL,
          'maxlength': res.RE0015.MSG_VALIDATION.MAX_LENGHT_EMAIL,
          'pattern0': res.RE0015.MSG_VALIDATION.EMAIL,
          'pattern1': res.RE0015.MSG_VALIDATION.LOGINID
        },
        'managementAuthority': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED
        },
        'functionAuthoritySet': {
          'required': res.RE0015.MSG_VALIDATION.REQUIRED,
          'matchAuthority': res.RE0015.MSG_VALIDATION.ERROR_AUTHORITY
        }
      };
    });
  }
}



