import {Type} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Observer} from 'rxjs/Observer';
import {Subject} from 'rxjs/Subject';

/**
 * ダイアログを制御するパラメータ
 */
export class DialogParams {
  public componentType: Type<any>;
  public icon: string;
  public closeButton: boolean;
  public hasTitle: boolean;
  public title: string;
  public message: string;
  public html: string;
  public under_footer: string
  public positiveButton: string;
  public negativeButton: string;
  public subject: Subject<DialogResult>;
  public extraParams: any;
  public width: string

  constructor(componentType: Type<any>, icon: string, closeButton: boolean, title: string, message: string, html: string,
              under_footer: string, positiveButton: string, negativeButton: string, subject: Subject<DialogResult>,
              extraParams: any, width: string) {
    this.componentType = componentType;
    this.icon = icon;
    this.closeButton = closeButton;
    this.title = title;
    this.message = message;
    this.html = html;
    this.under_footer = under_footer;
    this.positiveButton = positiveButton;
    this.negativeButton = negativeButton;
    this.subject = subject;
    this.extraParams = extraParams;
    this.width = width;
  }
}

/**
 * ダイアログ返却値のコンテナ
 */
export class DialogResult {
  public buttonName: string;
  public payload: any;

  constructor(buttonName: string, payload: any) {
    this.buttonName = buttonName;
    this.payload = payload;
  }

  isOk() {
    return this.buttonName === 'ok';
  }

  isCancel() {
    return this.buttonName === 'cancel';
  }
}

/**
 * モーダルダイアログのコンポーネントが実装すべきインターフェイス
 */
export interface DialogAdapter {
  params: DialogParams;
  passError: boolean;

  onModalInit();

  /**
   * ダイアログ特有「の返却データを取得する
   */
  getPayload(): any;
}



