import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-tel-input',
  templateUrl: './tel-input.component.html',
  styleUrls: ['./tel-input.component.scss']
})
export class TelInputComponent implements OnInit {
  public telFirst: string;
  public telMiddle: string;
  public telLast: string;
  @Input() inputOptions: any = {
    'name': null,
    'id': null,
    'error': null
  };

  constructor() { }

  ngOnInit() {
    // TODO mock
    this.telFirst = '090';
    this.telMiddle = '0000';
    this.telLast = '0000'
  }

  telChange(value: string) {

  }

}



