export class EventDetailsSettingValidator {
  public title: string;
  public place: string;
  public allDay: boolean;
  public startDay: any;
  public startTime: string;
  public endDay: string;
  public endTime: string;
  public repeatOption: string;
  public summary: string;
  public repeatInterval: number;
  public sunday: boolean;
  public monday: boolean;
  public tuesday: boolean;
  public wednesday: boolean;
  public thursday: boolean;
  public friday: boolean;
  public saturday: boolean;
  public endDate: string;
  public countTime: number;
  public untilDay: string;
  public monthRepeatOption: string;
  public optionShare: string;
  public userId: string;
  public publishType: string;
  public meetingPublishType: string;
  public groupId: string;
  public userCreated: string;
  public userUpdated: string;
  public note: string;

  constructor() {
    this.title = null;
    this.place = null;
    this.allDay = false;
    this.startDay = null;
    this.startTime = null;
    this.endDay = null;
    this.endTime = null;
    this.repeatOption = 'NONE';
    this.summary = null;
    this.repeatInterval = 1;
    this.sunday = true;
    this.monday = false;
    this.tuesday = false;
    this.wednesday = false;
    this.thursday =  false;
    this.friday = false;
    this.saturday = false;
    this.endDate = 'NONE';
    this.countTime = null;
    this.untilDay = null;
    this.monthRepeatOption = 'BYMONTHDAY';
    this.optionShare = 'MY';
    this.userId = null;
    this.publishType = 'VISIBLE';
    this.meetingPublishType = 'VISIBLE';
    this.groupId = null;
    this.userCreated = null;
    this.userUpdated = null;
    this.note = null;
  }
}



