import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {DialogService} from '../../../services/dialog.service';
import {GroupService} from '../../../services/group.service';
import {CreatePollSurveyValidator} from '../gr0009-page/gr0009-page.validator';
import {Gr0009PageComponent} from '../gr0009-page/gr0009-page.component';
import {EditPollSurveySettings} from '../../../models/gr/edit-poll-survey-settings';

@Component({
  selector: 'app-gr0010-page',
  templateUrl: './gr0010-page.component.html',
  styleUrls: ['./gr0010-page.component.scss'],
  providers: [CreatePollSurveyValidator]
})
export class Gr0010PageComponent implements OnInit {

  editSurveyForm: FormGroup;
  gr0009Validator;
  gr0009;
  formError = {
    contents: '',
    closeDate: '',
    closeTime: ''
  };
  closeTime;
  defaultDate;

  constructor(private translate: TranslateService,
              private fb: FormBuilder,
              private dialogService: DialogService,
              private groupService: GroupService,
              private createPollSurveyValidator: CreatePollSurveyValidator) {
    this.gr0009Validator = new CreatePollSurveyValidator(this.translate, this.fb);
    this.gr0009 = new Gr0009PageComponent(this.translate, this.fb, this.dialogService, this.createPollSurveyValidator, this.groupService);
    this.editSurveyForm = this.fb.group({
      'contents': ['', Validators.compose([Validators.required, Validators.maxLength(2000)])],
      'closeDate': [''],
      'closeTime': ['']
    });
  }

  ngOnInit() {
    this.defaultDate = new Date();
    this.editSurveyForm = this.fb.group({
      'contents': ['', Validators.compose([Validators.required, Validators.maxLength(2000)])],
      'closeDate': [''],
      'closeTime': ['']
    });
    this.closeTime = this.gr0009.genTimePicker();
  }

  putEditPollSurvey() {
    const result = this.createPollSurveyValidator.checkValidate(this.editSurveyForm, this.formError);
    this.formError = result.formErrors;
    if (result.checkValidForm) {
      this.dialogService.setLoaderVisible(true);
      const modelSendApi = new EditPollSurveySettings();
      modelSendApi.id = 'string';
      modelSendApi.contents = this.editSurveyForm.get('contents').value;
      modelSendApi.closeDate = this.gr0009.combineDateTime(this.editSurveyForm);
      this.groupService.putEditPollSurvey(modelSendApi).subscribe(
        (res) => {
          this.dialogService.setLoaderVisible(false);
          // redirect to list articles
        }, error => {
         this.dialogService.setLoaderVisible(false);
         this.dialogService.showError('MSG.ERROR');
        });
    }

  }

}



