import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'searchFilter',
  pure: false
})

export class SearchFilter implements PipeTransform {
  transform(datas: any[], field: string): any[] {
    if (field && datas) {
      const filterKey = Object.keys(field);
      return datas.filter(item => {
        return filterKey.some((keyName) => {
          return new RegExp(field[keyName], 'gi').test(item[keyName]) || field[keyName] === '';
        });
      });
    } else {
      return datas;
    }
  }
}



