import {Component, OnInit} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {RegistrationService} from '../../../services/registration.service';
import {PrHandlingHospitalsSettings} from '../../../models/re/pr-handling-hospitals-settings';
import {DialogService} from '../../../services/dialog.service';
import {ActivatedRoute} from '@angular/router';
import {Helper} from '../../../common/helper';
import {HttpStatusCode} from '../../../common/http_status_code';
import {HanlingHospitalSendApi} from '../../../models/re/hanling-hospital-send-api';
import {DialogResult} from '../../../models/dialog-param';
import {HttpStatus} from '../../../common/http.status';
import {Response} from '@angular/http';
import {HttpError} from '../../../common/error/http.error';
import {isNullOrUndefined} from 'util';

declare const $: any;

@Component({
  selector: 'app-re0012-page',
  templateUrl: './re0012-page.component.html',
  styleUrls: ['./re0012-page.component.scss'],
})

export class Re0012PageComponent implements OnInit {
  model = new PrHandlingHospitalsSettings();
  handlingHospitals: any[];
  handlingHospitalAnothers: any[];
  keyCode: string;
  errorResponse: string;
  errorResponseAther: string;
  contentAlertDelete: string;
  hospitalAnothersConvert = [];
  listHospitalAnothers = [];
  listHospital = [];
  statusBtnAdd: boolean;
  translate = {};
  accessToken: string;
  searchText: string;
  selected2: any;
  options: any;
  notDataHospitals: boolean;
  notDataHospitalAther: boolean;
  noResult: string;
  maximumSelected: string;
  inputLong: string;

  constructor(private registrationService: RegistrationService, private dialogService: DialogService,
              private translateService: TranslateService, private activatedRoute: ActivatedRoute) {
  }

  ngOnInit() {
    this.clickSelect2();
    this.searchMedicalOffices();
    this.dialogService.setLoaderVisible(true);
    this.activatedRoute.params.subscribe((param: any) => {
      this.accessToken = param['access_token'];
    });
    if (this.accessToken !== undefined && this.accessToken !== '') {
      this.createHandlingHospitalAuto(this.accessToken);
    } else {
      this.reload();
    }
    if (this.translateService.currentLang === 'en') {
      this.searchText = 'Search';
      this.noResult = 'No Results Found';
      this.maximumSelected = 'You can only select 1 item';
    } else {
      this.searchText = '検索';
      this.noResult = '一致する会社がありません';
      this.maximumSelected = '1つのアイテムのみを選択できます';
      this.inputLong = 'ここに文字を入力しないでください';
    }
    this.translateService.get('MSG').subscribe((res) => {
      this.translate = res;
    });
    this.options = {
      multiple: true,
      maximumSelectionLength: 1,
      placeholder: this.searchText,
      tags: false,
      maximumInputLength: 25,
      width: '100%',
      language: {
        noResults: () => {
          return this.noResult;
        }, maximumSelected: () => {
          return this.maximumSelected;
        },
        inputTooLong: () => {
          return this.inputLong;
        }
      }
    };
  }

  clickSelect2() {
    const selectBorder = $('.select2-container--default.select2-container--focus .select2-selection--multiple');
    if (selectBorder.attr('aria-expanded', true)) {
      selectBorder.css('border', '1px #018aff solid');
    }
    $(document).on('click', 'body', (e) => {
      const selectBorder2 = $('.select2-container--default .select2-selection--multiple');
      if (e.target.className !== 'select2-search__field') {
        selectBorder.css('border', '1px solid #aaa');
      }
      if (e.target.className === 'select2-selection__rendered') {
        selectBorder2.css('border', '1px #018aff solid');
      }
    });
  }

  reload() {
    this.keyCode = '';
    this.selected2 = '';
    this.errorResponse = '';
    this.statusBtnAdd = true;
    $('.select2-selection--multiple').removeAttr('style');
    this.errorResponseAther = ' ';
    this.getPrHandlingHospitals();
  }

  getPrHandlingHospitals() {
    this.dialogService.setLoaderVisible(true);
    this.registrationService.getPrHandlingHospitalsSettings().subscribe((settings: PrHandlingHospitalsSettings) => {
        this.dialogService.setLoaderVisible(false);
        this.model = settings;
        this.listHospital = Object.keys(this.model).map(key => this.model[key]);
        this.handlingHospitals = this.listHospital.filter(hospital => hospital.otherHandling === false);
        (this.handlingHospitals.length > 0) ? (this.notDataHospitals = false) : (this.notDataHospitals = true);
        this.handlingHospitalAnothers = this.listHospital.filter(hospital => hospital.otherHandling === true);
        (this.handlingHospitalAnothers.length > 0) ? (this.notDataHospitalAther = false) : (this.notDataHospitalAther = true);
      },
      error => {
        this.dialogService.setLoaderVisible(false);
      });
  }

  alertDeleteHospital(handlingHospital: any, index: number) {
    console.log(handlingHospital);
    (handlingHospital.otherHandling === false) ?
      (this.contentAlertDelete = this.translate['RE0012']['E001_1']) : (this.contentAlertDelete = this.translate['RE0012']['E003_1']);
    const contentDialog = this.contentAlertDelete.replace('hospitalName', handlingHospital.officeName);
    this.dialogService.showMessage('warning', false, null, null, contentDialog, null, 'MSG.YES', 'MSG.NO').subscribe(
      (result: DialogResult) => {
        setTimeout(() => {
          if (result.buttonName === Helper.dialog_result_ok) {
            this.deleteHospital(handlingHospital.officeId, handlingHospital.otherHandling, index);
          }
        }, 200);
      });
  }

  deleteHospital(officeId: string, otherHandling: boolean, index: number) {
    this.dialogService.setLoaderVisible(true);
    this.registrationService.deleteHandlingOtherHospitals(officeId).subscribe((response) => {
        console.log(response);
        if (response === HttpStatus.NotContent) {
          this.dialogService.setLoaderVisible(false);
          setTimeout(() => {
            this.dialogService.showMessage('success', false, null, 'MSG.DELETED', null, null, 'MSG.OK', null);
            if (otherHandling) {
              this.handlingHospitalAnothers.splice(index, 1);
              (this.handlingHospitalAnothers.length > 0) ? (this.notDataHospitalAther = false) : (this.notDataHospitalAther = true);
            } else {
              this.handlingHospitals.splice(index, 1);
              (this.handlingHospitals.length > 0) ? (this.notDataHospitals = false) : (this.notDataHospitals = true);
            }
            $('.select2-selection--multiple').removeAttr('style');
            this.errorResponseAther = ' ';
            this.errorResponse = '';
          }, 200);
        }
      },
      error => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('MSG.ERROR');
      });
  }

  createHandlingHospitalAuto(access_token: string) {
    this.dialogService.setLoaderVisible(true);
    const handlingHospitalSendApi = new HanlingHospitalSendApi(access_token, '', '', '');
    this.registrationService.createHandlingHospitals(handlingHospitalSendApi).subscribe(
      response => {
        setTimeout(() => {
          this.dialogService.setLoaderVisible(false);
          this.dialogService.showMessage('success', false, null, 'MSG.ADDED', null, null, 'OK', null);
          this.reload();
        }, 200);
      },
      error => {
        setTimeout(() => {
          this.dialogService.setLoaderVisible(false);
          this.reload();
        }, 200);
      });
  }

  createHandlingHospitalByKeyCode(keyCode: string) {
    this.dialogService.setLoaderVisible(true);
    const handlingHospitalSendApi = new HanlingHospitalSendApi('', keyCode, '', '');
    this.registrationService.createHandlingHospitals(handlingHospitalSendApi).subscribe(
      response => {
        this.dialogService.setLoaderVisible(false);
        setTimeout(() => {
          this.dialogService.showMessage('success', false, null, 'MSG.ADDED', null, null, 'OK', null);
        }, 200);
        this.reload();
      },
      (error: HttpError) => {
        setTimeout(() => {
          this.dialogService.setLoaderVisible(false);
          switch (error.error_codes[0]) {
            case 'COMMON.SAVE_FAILED':
              this.errorResponse = this.translate['RE0012']['E002_1'];
              break;
            case 'R0012.E002_1':
              this.errorResponse = this.translate['RE0012']['E002_1'];
              break;
            case 'R0012.E002_2':
              this.errorResponse = this.translate['RE0012']['E002_2'];
              break;
          }
          this.reloadWhenError();
        }, 200);
      });
  }

  createHandlingOtherHospitals() {
    const otherHospitalsId = this.selected2;
    if (otherHospitalsId === '' || otherHospitalsId === 'undefined') {
      this.errorResponseAther = '担当病院の名前をを入力してください';
      $('.select2-selection--multiple').css('border', '1px solid #D21D29');
    } else {
      this.dialogService.setLoaderVisible(true);
      const handlingHospitalSendApi = new HanlingHospitalSendApi('', '', otherHospitalsId[0].id, otherHospitalsId[0].text);
      this.registrationService.createHandlingOtherHospitals(handlingHospitalSendApi).subscribe(
        respone => {
          this.dialogService.setLoaderVisible(false);
          console.log(respone);
          setTimeout(() => {
            this.dialogService.showMessage('success', false, null, 'MSG.SAVED', null, null, 'MSG.YES', null);
            this.reload();
          }, 200);
        },
        error => {
          this.dialogService.setLoaderVisible(false);
          if (error.error_codes[0] === 'R0012.E005_1') {
            this.errorResponseAther = this.translate['RE0012']['E005_1'];
            $('.select2-selection--multiple').css('border', '1px solid #D21D29');
          }
        });
    }
  }

  searchMedicalOffices() {
    this.dialogService.setLoaderVisible(true);
    this.registrationService.getSearchMedicalOffices('').subscribe((settings: PrHandlingHospitalsSettings[]) => {
        // Convert data to formatted data of select2
        this.dialogService.setLoaderVisible(false);
        for (const setting of settings) {
          const objHospitalAnother = {};
          objHospitalAnother['text'] = setting['medicalName'];
          objHospitalAnother['id'] = setting['medicalOfficeId'];
          this.hospitalAnothersConvert.push(objHospitalAnother);
        }
        this.listHospitalAnothers = this.hospitalAnothersConvert;
      },
      error => {
        this.dialogService.setLoaderVisible(false);
      });
  }

  reloadWhenError() {
    this.selected2 = ' ';
    this.statusBtnAdd = true;
    $('.select2-selection--multiple').removeAttr('style');
    this.errorResponseAther = ' ';
    this.getPrHandlingHospitals();
  }

}



