import {Injectable} from '@angular/core';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {EventCreateSettings} from '../../../models/ca/event-create-settings';
import {EventCreateSettingValidator} from '../../../models/ca/event-create-setting-validator';
import {TranslateService} from '@ngx-translate/core';
import * as moment from 'moment';
import {CalendarCommon} from '../../../common/calendar-common';

@Injectable()
export class Ca0002PageValidator {
  valueShare = {'user': 'MY', 'insideHospital': 'INSIDE_GROUP', 'outsideHospital': 'OUTSIDE_GROUP'};

  constructor(private formBuilder: FormBuilder, private translate: TranslateService, private calendarCommon: CalendarCommon) {
  }

  createForm(model: EventCreateSettingValidator) {
    let form: FormGroup;
    form = this.formBuilder.group({
      'title': [model.title],
      'allDay': [model.allDay],
      'startDay': [model.startDay, [
        Validators.required
      ]
      ],
      'startTime': [model.startTime, [
        Validators.required
      ]
      ],
      'endDay': [model.endDay, [
        Validators.required
      ]
      ],
      'endTime': [model.endTime, [
        Validators.required
      ]
      ],
      'optionShare': [model.optionShare],
      'userId': [model.userId],
      'publishType': [model.publishType],
      'meetingPublishType': [model.meetingPublishType],
      'groupId': [model.groupId],
    });
    return form;
  }

  setValueForm(start, end, defaultView) {
    let checkAllDay = false;
    const form = new EventCreateSettingValidator();

    if (defaultView === 'agendaWeek') {
      if (moment(start).format('HH:mm') === moment(end).format('HH:mm') && moment(end).format('HH:mm') === '00:00') {
        end = moment(end).add(-1, 'days').format();
        form.allDay = true;
        checkAllDay = true;
      } else {
        if (moment(start).clone().add(0.5, 'hours').format('YYYYMMDDHHss') === moment(end).format('YYYYMMDDHHss')) {
          const timeStart = moment(start).format('HH:00:00');
          const timeEnd = moment(end).clone().add(0.5, 'hours').format('HH:00:00');
          start = moment(start).format('YYYY-MM-DDT' + timeStart);
          end = moment(end).clone().add(1, 'hours').format('YYYY-MM-DDT' + timeEnd);
        }
      }
    } else {
      if (moment(start).format('YYYYMMDD') === moment(end).add(-1, 'days').format('YYYYMMDD')) {
        const date = moment(start).format();
        const currentDate = new Date();
        const timeStart = moment(currentDate).format('HH:00:00');
        const timeEnd = moment(currentDate).clone().add(1, 'hours').format('HH:00:00');

        start = moment(date).format('YYYY-MM-DDT' + timeStart);
        end = moment(date).clone().add(1, 'hours').format('YYYY-MM-DDT' + timeEnd);
      } else {
        if (moment(start).format('HH:mm') === moment(end).format('HH:mm') && moment(end).format('HH:mm') === '00:00') {
          end = moment(end).add(-1, 'days').format();
          form.allDay = true;
          checkAllDay = true;
        }
      }
    }

    const createEventForm = this.createForm(form);
    const startForm = this.calendarCommon.convertDatetime(start);
    const endForm = this.calendarCommon.convertDatetime(end);

    createEventForm.controls.startDay.setValue(startForm.date);
    createEventForm.controls.startTime.setValue(startForm.time);
    createEventForm.controls.endDay.setValue(endForm.date);
    createEventForm.controls.endTime.setValue(endForm.time);

    return {
      'form': createEventForm,
      'checkAllDay': checkAllDay,
      'start': start,
      'end': end,
      'allDay': form.allDay
    };
  }

  /*
   *check validate form after submit
   */
  checkValidate(createEventForm: FormGroup, formMessagesError: any) {
    if (!createEventForm) {
      return false;
    }
    const form = createEventForm;
    const startDay = form.get('startDay').value;
    const startTime = form.get('startTime').value;
    const endDay = form.get('endDay').value;
    const endTime = form.get('endTime').value;
    if (startDay === null) {
      formMessagesError.startDay = true;
    } else {
      formMessagesError.startDay = false;
    }
    if (startTime === null) {
      formMessagesError.startTime = true;
    } else {
      formMessagesError.startTime = false;
    }
    if (endDay === null) {
      formMessagesError.endDay = true;
    } else {
      formMessagesError.endDay = false;
    }
    if (endTime === null) {
      formMessagesError.endTime = true;
    } else {
      formMessagesError.endTime = false;
    }
    if (startDay !== null && startTime !== null && endDay !== null && endTime !== null) {
      if (this.calendarCommon.convertDateTimeUTC(startDay, startTime) >= this.calendarCommon.convertDateTimeUTC(endDay, endTime)) {
        formMessagesError.timeMatch = true;
      } else {
        formMessagesError.timeMatch = false;
      }
    }

  }

  getDataForm(createEventForm: FormGroup) {
    if (!createEventForm) {
      return false;
    }
    let data: any;
    data = createEventForm.value;
    return data;
  }

  mapModel(formModel: EventCreateSettingValidator, model: EventCreateSettings) {
    this.translate.get('CA004').subscribe((response) => {
      model.id = '';
      model.title = formModel.title ? formModel.title : response.TITLE.DEFAULT_VALUE;
      model.allDay = formModel.allDay;
      if (formModel.allDay === true) {
        model.start = this.calendarCommon.convertDateTimeIsoAllDay(formModel.startDay);
        model.end = this.calendarCommon.convertDateTimeIsoAllDay(formModel.endDay);
      } else {
        model.start = this.calendarCommon.convertDateTimeIso(formModel.startDay, formModel.startTime);
        model.end = this.calendarCommon.convertDateTimeIso(formModel.endDay, formModel.endTime);
      }
      model.meetingPublishType = formModel.meetingPublishType;
      model.publishType = formModel.publishType;
      if (formModel.optionShare === this.valueShare.user) {
        model.userId = formModel.userId;
        model.groupId = '';
      } else if (formModel.optionShare === this.valueShare.insideHospital) {
        model.groupId = formModel.groupId;
        model.userId = '';
      }
      model.note = '';
      model.place = '';
      model.recursiveOption = 'THIS_EVENT';
      model.repeatParentId = '';
      model.repeatRule = '';
    });
  }
}





