import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {DialogService} from '../../../services/dialog.service';
import {GroupService} from '../../../services/group.service';
import {CreateDepartmentGroupSettings} from '../../../models/gr/create-department-group-settings';
import {DepartmentUserSettings} from '../../../models/gr/department-user-settings';
import {SearchFilter} from '../shared/search-filter.pipe';
import {Helper} from '../../../common/helper';
import {CreateDepartmentGroupSettingsSendApi} from '../../../models/gr/create-department-group-settings-send-api';
import {DialogResult} from '../../../models/dialog-param';
import {SharedValueService} from '../../../services/shared-value.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-gr0003-page',
  templateUrl: './gr0003-page.component.html',
  styleUrls: ['./gr0003-page.component.scss'],
  providers: [SearchFilter]
})
export class Gr0003PageComponent implements OnInit {

  public createDepartModel = new CreateDepartmentGroupSettings();
  createGroupForm: FormGroup;
  listMember = [];
  listAdmin = [];
  listDepartment = [];
  formErrors = {
    'name': '',
    'member': '',
    'deptId': ''
  };
  nonSelectedAdmin = false;
  selectedDepartmentId;
  userSession;

  constructor(private translate: TranslateService,
              private fb: FormBuilder,
              private dialogService: DialogService,
              private groupService: GroupService,
              private helper: Helper,
              private sharedValueService: SharedValueService,
              private route: Router) {
    this.createGroupForm = this.fb.group({
      'name': ['', Validators.compose([Validators.required, Validators.maxLength(20), Validators.pattern(Helper.regexGroupName)])],
      'deptId': ['', Validators.required]
    });
  }

  ngOnInit() {
    this.dialogService.setLoaderVisible(true);
    this.userSession = this.sharedValueService.fetchUserSession();
    this.groupService.getCreateDepartmentGroup().subscribe(
      (settings: CreateDepartmentGroupSettings) => {
        this.dialogService.setLoaderVisible(false);
        this.createDepartModel = settings;
        this.createForm();
      },
      error => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('MSG.ERROR');
      }
    );
  }

  createForm() {
    this.createGroupForm = this.fb.group({
      'name': ['', Validators.compose([Validators.required, Validators.maxLength(20), Validators.pattern(Helper.regexGroupName)])],
      'deptId': ['', Validators.required]
    });
    this.listDepartment = this.helper.createArrayDepartment(this.listDepartment, this.createDepartModel.departments, '', 1);
  }

  checkAuthority() {
    // pending
  }

  gotoCreateHospitalGr() {
    this.route.navigateByUrl('/gr/gr0002');
  }

  changeDepartment(deptId) {
    if (deptId === '') {
      this.listMember = [];
    } else {
      this.listMember = [];
      this.listAdmin = [];
      this.selectedDepartmentId = deptId;
      this.groupService.getDepartmentUser(deptId).subscribe(
        (res: DepartmentUserSettings[]) => {
          this.listMember = this.helper.convertUserName(this.listMember, res);
          for (let i = 0; i < this.listMember.length; i++) {
            this.listAdmin.push(false);
          }
        },
        error => {
          this.dialogService.showError('MSG.ERROR');
        }
      );
    }
  }

  selectedDepartmentAdmin(event, idx) {
    this.listAdmin = this.helper.selectedAdmin(event.target.checked, idx, this.listAdmin);
  }

  checkValidate(form, formErrors, listMember, listAdmin, nonSelectedAdmin) {
    let checkSelectedMember = false;
    let arrayIsAdmin = [];
    if (!form) {
      return;
    } else {
      for (const field in formErrors) {
        formErrors[field] = '';
        const control = form.get(field);
        if (control && control.invalid) {
          if (field === 'name') {
            formErrors[field] = this.translate.instant('VAL.WRONG_FORMAT');
          }
          if (field === 'deptId') {
            formErrors[field] = this.translate.instant('VAL.NON_SELECTED_DEPT');
          }
        }
        if (form.get('name').value.trim() === '') {
          formErrors['name'] = this.translate.instant('VAL.WRONG_FORMAT');
        }
        if (field === 'member') {
          if (listMember.length === 0) {
            checkSelectedMember = false;
            formErrors[field] = this.translate.instant('VAL.NON_SELECTED');
          } else {
            checkSelectedMember = true;
            formErrors[field] = '';
          }
        }
      }
      if (listMember.length > 0) {
        if (listAdmin.length > 0) {
          arrayIsAdmin = listAdmin.filter((item) => item === true);
          if (arrayIsAdmin.length === 0) {
            nonSelectedAdmin = true;
          } else {
            nonSelectedAdmin = false;
          }
        } else {
          for (let k = 0; k < listMember.length; k++) {
            if (listMember[k].authority === 'ADMIN') {
              nonSelectedAdmin = false;
              break;
            } else {
              nonSelectedAdmin = true;
            }
          }
        }
        if (nonSelectedAdmin) {
          this.dialogService.showError('VAL.ADMIN_GROUP');
        }
      }
      return {formErrors: formErrors, nonSelectedAdmin: nonSelectedAdmin, checkSelectedMember: checkSelectedMember};
    }
  }

  postDepartmentGroupettings() {
    const results = this.checkValidate(this.createGroupForm, this.formErrors, this.listMember, this.listAdmin , this.nonSelectedAdmin);
    this.formErrors = results.formErrors;
    this.nonSelectedAdmin = results.nonSelectedAdmin;
    if (this.createGroupForm.valid && !this.nonSelectedAdmin && results.checkSelectedMember) {
      this.dialogService.setLoaderVisible(true);
      const modelSendApi = new CreateDepartmentGroupSettingsSendApi();
      modelSendApi.name = this.createGroupForm.get('name').value;
      modelSendApi.imageFlg = true;
      modelSendApi.deptId = this.selectedDepartmentId;
      for (let i = 0; i < this.listMember.length; i++) {
        const mem = {};
        mem['id'] = this.listMember[i].userId;
        mem['adminFlg'] = this.listAdmin[i];
        modelSendApi.users.push(mem);
      }
      this.groupService.postCreateDepartmentGroup(modelSendApi).subscribe(
        (response) => {
          this.dialogService.setLoaderVisible(false);
          this.dialogService.showMessage('success', false, null, 'MSG.GR.M001', null, null, 'MSG.YES', null).subscribe(
            (res: DialogResult) => {
              if (res.buttonName === Helper.dialog_result_ok) {
                // redirect to group's timeline page
              }
            }
          );
        }, error => {
          this.dialogService.setLoaderVisible(false);
          this.dialogService.showError('MSG.ERROR');
        }
      );
    }
  }
}



