import {Component, OnInit} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {CreateHospitalGroupSettings} from '../../../models/gr/create-hospital-group-settings';
import {DialogService} from '../../../services/dialog.service';
import {GroupService} from '../../../services/group.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Helper} from '../../../common/helper';
import {CreateHospitalGroupSettingsSendApi} from '../../../models/gr/create-hospital-group-settings-send-api';
import {DialogResult} from '../../../models/dialog-param';
import {SharedValueService} from '../../../services/shared-value.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-gr0002-page',
  templateUrl: './gr0002-page.component.html',
  styleUrls: ['./gr0002-page.component.scss'],
})
export class Gr0002PageComponent implements OnInit {
  public model = new CreateHospitalGroupSettings();
  listUser = [];
  listDepartment = [];
  createGroupForm: FormGroup;
  checkAllUser = false;
  listSelectedUser = [];
  selectedUserRow = [];
  listMember = [];
  listSelectedMember = [];
  selectedMemberRow = [];
  listAdmin = [];
  groupNameError = '';
  nonSelectedAdmin = false;
  isVisiableBtn = false;
  userSession;
  userLogin = {
    userId: 'user0000',
    deptId: 'dept0005',
    lastName: 'userLogin',
    firstName: 'firstName',
    lastNameKana: 'はな',
    firstNameKana: 'み'
  };

  constructor(private translate: TranslateService,
              private dialogService: DialogService,
              private fb: FormBuilder,
              private groupService: GroupService,
              private helper: Helper,
              private sharedValueService: SharedValueService,
              private route: Router) {
    this.createGroupForm = this.fb.group({
      'name': ['', Validators.compose([Validators.required, Validators.maxLength(20), Validators.pattern(Helper.regexGroupName)])],
      'deptId': ''
    });
  }

  ngOnInit() {
    this.dialogService.setLoaderVisible(true);
    this.userSession = this.sharedValueService.getUserSession();
    this.groupService.getCreateHospitalGroup().subscribe(
      (settings: CreateHospitalGroupSettings) => {
        this.dialogService.setLoaderVisible(false);
        this.model = settings;
        this.createForm();
      },
      error => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('MSG.ERROR');
      }
    );
  }

  createForm() {
    this.createGroupForm = this.fb.group({
      'name': ['', Validators.compose([Validators.required, Validators.maxLength(20), Validators.pattern(Helper.regexGroupName)])],
      'deptId': ''
    });
    this.listDepartment = this.helper.createArrayDepartment(this.listDepartment, this.model.departments, '', 1);
    this.listUser = this.helper.convertUserName(this.listUser, this.model.departmentUsers);
    for (let i = 0; i < this.listUser.length; i++) {
      this.selectedUserRow.push(false);
    }
    this.listMember = this.helper.convertUserName(this.listMember, this.userLogin);
    this.listAdmin.push(true);
  }

  checkAuthority() {
    // pending
  }

  gotoCreateDeptGroup() {
    this.route.navigateByUrl('/gr/gr0003');
  }

  selectedHospitalAdmin(event, idx) {
    this.listAdmin = this.helper.selectedAdmin(event.target.checked, idx, this.listAdmin);
  }

  getDepartmentUserInHospitalGroup(deptId, userInfoModel, listMember) {
    let listUser = [];
    let listConvertedUser = [];
    const selectedUserRow = [];
    if (deptId === '') {
      listConvertedUser = this.helper.convertUserName(listConvertedUser, userInfoModel);
    } else {
      listUser = [];
      for (let i = 0; i < userInfoModel.length; i++) {
        if (userInfoModel[i].deptId === deptId) {
          listUser.push(userInfoModel[i]);
        }
      }
      listConvertedUser = this.helper.convertUserName(listConvertedUser, listUser);
    }
    // filter selected users
    for (let i = 0; i < listMember.length; i++) {
      listConvertedUser = listConvertedUser.filter((item) => item.userId !== listMember[i].userId);
    }
    for (let i = 0; i < listConvertedUser.length; i++) {
      selectedUserRow.push(false);
    }
    return {'listUser': listConvertedUser, 'selectedUserRow': selectedUserRow};
  }

  grChangeDepartment(deptId) {
    const result = this.getDepartmentUserInHospitalGroup(deptId, this.model.departmentUsers, this.listMember);
    this.listUser = result.listUser;
    this.selectedUserRow = result.selectedUserRow;
  }

  selectedRow(user, index, listSelectedRow, listSelectedItem) {
    if (listSelectedRow[index]) {
      listSelectedRow[index] = false;
    } else {
      listSelectedRow[index] = true;
    }
    const arrayDupli = listSelectedItem.filter((item) => item === user.userId);
    if (arrayDupli.length > 0) {
      listSelectedItem = listSelectedItem.filter((i) => arrayDupli.indexOf(i) === -1);
    } else {
      listSelectedItem.push(user.userId);
    }
    return [listSelectedRow, listSelectedItem];
  }

  selectedUser(user, index) {
    const result = this.selectedRow(user, index, this.selectedUserRow, this.listSelectedUser);
    this.selectedUserRow = result[0];
    this.listSelectedUser = result[1];
  }

  selectedMember(user, index) {
    const result = this.selectedRow(user, index, this.selectedMemberRow, this.listSelectedMember);
    this.selectedMemberRow = result[0];
    this.listSelectedMember = result[1];
  }

  changeCheckAllCommon(event, checkAllUser, listSelectedUser, listUser, selectedUserRow) {
    if (event.target.checked) {
      checkAllUser = true;
      listSelectedUser = [];
      selectedUserRow = [];
      for (let i = 0; i < listUser.length; i++) {
        listSelectedUser.push(listUser[i].userId);
        selectedUserRow.push(true);
      }
    } else {
      checkAllUser = false;
      selectedUserRow.fill(false);
      listSelectedUser = [];
    }
    return {
      checkAllUser: checkAllUser,
      listSelectedUser: listSelectedUser,
      listUser: listUser,
      selectedUserRow: selectedUserRow
    };
  }

  changeCheckAll(event) {
    const result = this.changeCheckAllCommon(event, this.checkAllUser, this.listSelectedUser, this.listUser, this.selectedUserRow);
    this.checkAllUser = result.checkAllUser;
    this.listSelectedUser = result.listSelectedUser;
    this.listUser = result.listUser;
    this.selectedUserRow = result.selectedUserRow;
  };

  addMember() {
    for (let i = 0; i < this.listSelectedUser.length; i++) {
      for (let j = 0; j < this.listUser.length; j++) {
        if (this.listSelectedUser[i] === this.listUser[j].userId) {
          this.listMember.push(this.listUser[j]);
          this.selectedMemberRow.push(false);
          this.listUser.splice(j, 1);
          this.listAdmin.push(false);
        }
      }
    }
    this.checkAllUser = false;
    this.listSelectedUser = [];
    this.selectedUserRow = [];
  }


  removeMember() {
    const selectedDeptId = this.createGroupForm.get('deptId').value;
    for (let i = 0; i < this.listSelectedMember.length; i++) {
      for (let j = 0; j < this.listMember.length; j++) {
        if (!this.listAdmin[j] && this.listSelectedMember[i] === this.listMember[j].userId) {
          if (selectedDeptId === '' || this.listMember[j].deptId === selectedDeptId) {
            this.listUser.push(this.listMember[j]);
          }
          this.selectedUserRow.push(false);
          this.listMember.splice(j, 1);
          this.listAdmin.splice(j, 1);
        }
      }
    }
    this.checkAllUser = false;
    this.listSelectedMember = [];
    this.selectedMemberRow = [];
  }

  checkValidate(form: FormGroup, messError, listMember, listAdmin, nonSelectedAdmin) {
    if (!form) {
      return;
    } else {
      messError = '';
      const control = form.get('name');
      if (control && (control.invalid || (control.value.trim() === ''))) {
        messError = this.translate.instant('VAL.WRONG_FORMAT');
      } else {
        messError = '';
      }
      if (listMember.length > 0) {
        if (listAdmin.length > 0) {
          const arrayIsAdmin = listAdmin.filter((item) => item === true);
          if (arrayIsAdmin.length === 0) {
            nonSelectedAdmin = true;
          } else {
            nonSelectedAdmin = false;
          }
        } else {
          for (let i = 0; i < listMember.length; i++) {
            if (listMember[i].authority === 'ADMIN') {
              nonSelectedAdmin = false;
              break;
            } else {
              nonSelectedAdmin = true;
            }
          }
        }
        if (nonSelectedAdmin) {
          this.dialogService.showError('VAL.ADMIN_GROUP');
        }
      }
      return {'msgError': messError, 'nonSelectedAdmin': nonSelectedAdmin};
    }
  }

  createInsideGroup() {
    const resultCheckValidation = this.checkValidate(this.createGroupForm, this.groupNameError,
      this.listMember, this.listAdmin, this.nonSelectedAdmin);
    this.groupNameError = resultCheckValidation.msgError;
    this.nonSelectedAdmin = resultCheckValidation.nonSelectedAdmin;
    if (this.groupNameError === '' && !this.nonSelectedAdmin) {
      this.dialogService.setLoaderVisible(true);
      const modelSendApi = new CreateHospitalGroupSettingsSendApi();
      modelSendApi.name = this.createGroupForm.get('name').value;
      modelSendApi.imageFlg = false;
      for (let i = 0; i < this.listMember.length; i++) {
        const mem = {};
        mem['id'] = this.listMember[i].userId;
        mem['adminFlg'] = this.listAdmin[i];
        modelSendApi.users.push(mem);
      }
        this.groupService.postCreateHospitalGroup(modelSendApi).subscribe(
          (response) => {
            this.dialogService.setLoaderVisible(false);
            this.dialogService.showMessage('success', false, null, 'MSG.GR.M001', null, null, 'MSG.YES', null).subscribe(
              (res: DialogResult) => {
                if (res.buttonName === Helper.dialog_result_ok) {
                  // redirect to group's timeline page
                }
              }
            );
          }, error => {
            this.dialogService.setLoaderVisible(false);
            this.dialogService.showError('MSG.ERROR');
          });
    }
  }
}



