import { Component, OnInit, AfterViewInit, Renderer2 } from '@angular/core';
import { MeetingService } from '../../../services/meeting.service';
import { PicSettings } from '../../../models/me/pic-settings';
import { DialogService } from '../../../services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { DialogResult } from '../../../models/dialog-param';
import { Helper } from '../../../common/helper';
import { Subscriber } from 'rxjs/Subscriber';

declare const $: any;
@Component({
  selector: 'app-me0014-page',
  templateUrl: './me0014-page.component.html',
  styleUrls: ['./me0014-page.component.scss']
})
export class Me0014PageComponent implements OnInit, AfterViewInit {
  public model = new PicSettings();
  office: any;
  offices: any;
  indexTab = 1;

  // Params
  public department = '';
  public name = '';
  public flag = false;
  public page = '1';
  public size = '10';
  public sort = '';

  // Data when Scroll
  public dataScroll: any;
  public pageDefault = '1';

  constructor(
    private meetingService: MeetingService,
    private dialogService: DialogService,
    private translateService: TranslateService,
    private renderer: Renderer2) { }

  ngOnInit() {
    // this.loadListPic()
    this.scrollPaging();
  }

  loadListPic() {
    this.dialogService.setLoaderVisible(true);
    this.meetingService.getPicsSettings(this.department, this.name, this.sort, this.page, this.size, this.flag).subscribe(
      (settings: PicSettings) => {
        this.dialogService.setLoaderVisible(false);
        this.model = settings['user'];
        this.dataScroll = this.model;
        if (this.page === this.pageDefault) {
          this.offices = this.model;
        } else {
          this.offices = this.offices.concat(this.dataScroll);
        }
      });
  }

  // Scroll Paging
  scrollPaging() {
    this.loadListPic();
    this.renderer.listen('window', 'scroll', (evt) => {
      if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
        this.page = String(+this.page + 1);
        this.loadListPic();
      }
    });
  }

  ngAfterViewInit() {
    $('[data-toggle="tooltip"]').tooltip();
    $('[data-toggle="popover"]').popover({
      html: true
    });
  }

  searchOffice(name: string) {
    name ? (this.name = name) : (this.name = '');
    this.loadListPic();
  }

  putMeetingRestriction(statusConfirm, office, index) {
    const officeId = office.id;
    const setting = { officeId, statusConfirm };
    this.translateService.get('MSG.ME0014').subscribe((res) => {
      switch (statusConfirm) {
        case '1':
          const nameStaffReplace = office.firstName + ' ' + office.lastName;
          const officeName = res['E003_2'].replace('nameOffice', nameStaffReplace);
          this.dialogService.showMessage('warning', false, officeName, null, null, null, 'MSG.YES', 'MSG.NO').subscribe(
            (resp: DialogResult) => {
              if (resp.buttonName === Helper.dialog_result_ok) {
                this.dialogService.setLoaderVisible(true);
                this.meetingService.putMeetingRestrictionSettings(setting).subscribe(response => {
                  this.dialogService.setLoaderVisible(false);
                  this.dialogService.showSuccess('MSG.ME0014.SAVED_E1');
                });
              } else if (resp.buttonName === Helper.dialog_result_cancel) {
                $('.authority' + '_' + index).val(office.restrictionStatus);
              }
            });
          break;
        case '2':
          const nameReplace = office.firstName + ' ' + office.lastName;
          const officeName1 = res['E003_8'].replace('nameOffice', nameReplace);
          this.dialogService.showMessage('warning', false, officeName1, null, null, null, 'MSG.YES', 'MSG.NO').subscribe(
            (resp: DialogResult) => {
              if (resp.buttonName === Helper.dialog_result_ok) {
                this.dialogService.setLoaderVisible(true);
                this.meetingService.putMeetingRestrictionSettings(setting).subscribe(response => {
                  this.dialogService.setLoaderVisible(false);
                  this.dialogService.showSuccess('MSG.ME0014.SAVED_E2');
                });
              } else if (resp.buttonName === Helper.dialog_result_cancel) {
                $('.authority' + '_' + index).val(office.restrictionStatus);
              }
            });
          break;
        case '3':
          const nameStaff = office.firstName + ' ' + office.lastName;
          const officeName2 = res['E003_9'].replace('nameOffice', nameStaff);
          const title = '<div>' + res['E003_3'] + '</div>' + '<div>' + res['E003_4'] + '</div>' + '<div>' + res['E003_5'] + '</div>'
            + '<div>' + res['E003_6'] + '</div>' + '<div>' + res['E003_7'] + '</div>' + '<div>' + res['E003_71'] + '</div><br>' + officeName2;
          this.dialogService.showMessage('warning', false, null, null, title, null, 'MSG.YES', 'MSG.NO')
            .subscribe((resp: DialogResult) => {
              if (resp.buttonName === Helper.dialog_result_ok) {
                this.dialogService.setLoaderVisible(true);
                this.meetingService.putMeetingRestrictionSettings(setting).subscribe(response => {
                  this.dialogService.setLoaderVisible(false);
                  this.dialogService.showSuccess('MSG.ME0014.SAVED_E3');
                });
              } else if (resp.buttonName === Helper.dialog_result_cancel) {
                $('.authority' + '_' + index).val(office.restrictionStatus);
              }
            });
          break;
      }
    });
  }

  putIdentifyStatusConfirm(office, statusConfirm) {
    const officeId = office.id;
    const settings = { officeId, statusConfirm };
    this.dialogService.setLoaderVisible(true);
    const setting = { officeId, statusConfirm };
    this.meetingService.putMeetingRestrictionSettings(setting).subscribe(response => {
      this.dialogService.setLoaderVisible(false);
      this.dialogService.showSuccess('MSG.SAVED').subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.dialogService.setLoaderVisible(false);
            office.statusConfirm = '3';
          }
        });
    });
  }

  putIdentifyStatusCancel(office, statusConfirm) {
    const officeId = office.id;
    const settings = { officeId, statusConfirm };
    const nameStaffReplace = office.firstName + ' ' + office.lastName;
    this.translateService.get('MSG.ME0014').subscribe((res) => {
      const nameStaff = res['E003_1'].replace('nameOffice', nameStaffReplace);
      this.dialogService.showMessage('warning', false, nameStaff, null, null, null, 'MSG.YES', 'MSG.NO').subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.dialogService.setLoaderVisible(true);
            this.meetingService.putMeetingRestrictionSettings(settings).subscribe(response => {
              this.dialogService.setLoaderVisible(false);
              this.dialogService.showSuccess('MSG.SAVED');
              office.statusConfirm = '2';
            });
          }
        });
    });
  }

  toggleOffice(event) {
    if (!event.target.checked) {
      this.offices = Object.keys(this.model).map(key => this.model[key]);
    } else {
      this.office = Object.keys(this.model).map(key => this.model[key]);
      this.offices = this.office.filter(item => item.statusConfirm === '1');
    }
  }
}


