import { DialogService } from '../../../services/dialog.service';
import { Component, OnInit, HostListener, ElementRef } from '@angular/core';
import { MeetingService } from '../../../services/meeting.service';
import { Me0003PageValidator } from './me0003-page.validator';
import { SharedValueService } from '../../../services/shared-value.service';
import { TranslateService } from '@ngx-translate/core';
import { FormGroup } from '@angular/forms';
import { VisitEntrySettings } from '../../../models/me/visit-entry-settings';
import { VisitEntrySettingsSaveResult } from '../../../models/me/visit-entry-settings-save-result';
import { HttpError } from '../../../common/error/http.error';

@Component({
  selector: 'app-me0003-page',
  templateUrl: './me0003-page.component.html',
  styleUrls: ['./me0003-page.component.scss'],
  providers: [Me0003PageValidator]
})
export class Me0003PageComponent implements OnInit {

  public model = new VisitEntrySettings();
  public modelResponse: any;
  modelGroupForm: FormGroup;
  formErrors: any;
  visitNumbers: any;
  officeId = '01234567890';
  // Select 2 Meeting Visit
  selectedVisitMeeting: any;
  data: Array<any>;
  options: any;
  noResult: string;
  checkValidateSelect = false;
  // Datetime Picker
  timePicker = [];
  // Convert Date
  newDateStart: string;
  newDateEnd: string;
  dynamicId;
  selectEnd;

  constructor(
    private meetingService: MeetingService,
    private me0003Validator: Me0003PageValidator,
    private shareValue: SharedValueService,
    private translate: TranslateService,
    private dialogService: DialogService,
    private eref: ElementRef
  ) {
    this.formErrors = this.me0003Validator.formErrors;
    this.modelGroupForm = this.me0003Validator.me0003PageForm;
    this.visitNumbers = this.shareValue.getVisitNumber();
  }

  startDatepicker(id) {
    this.dynamicId = id;
  }
  endDatepicker(id) {
    this.selectEnd = id;
  }

  @HostListener('click', ['$event']) onClick(e) {
    if ((this.dynamicId !== undefined || this.selectEnd !== undefined) && this.eref.nativeElement.querySelector('ngb-datepicker')
      && !this.eref.nativeElement.querySelector('ngb-datepicker').contains(e.target)) {
      if (!this.eref.nativeElement.querySelector('.startDay').contains(e.target) && this.dynamicId) {
        this.dynamicId.close();
      }
      if (!this.eref.nativeElement.querySelector('.endDay').contains(e.target) && this.selectEnd) {
        this.selectEnd.close();
      }
    }
  }

  ngOnInit() {
    this.genTimePicker();
    if (this.translate.currentLang === 'en') {
      this.noResult = 'No Result';
    } else {
      this.noResult = '一致する会社がありません';
    }
    this.options = {
      placeholder: {
        id: '0',
        text: '',
        selected: 'selected'
      },
      language: {
        noResults: () => {
          return this.noResult;
        }
      }
    };
    this.loadVisitableUsers();
  }

  loadVisitableUsers() {
    this.meetingService.getVisitableUsersSettings(this.officeId).subscribe((settings: VisitEntrySettings) => {
      this.model = settings;
      const tempData = [];
      const placeholder = { id: '0', text: '' };
      tempData.push(placeholder);
      for (const child of this.model.users) {
        const objMedicalAnother = { 'text': '', 'id': '0' };
        objMedicalAnother['text'] = child.lastName + ' ' + child.firstName;
        objMedicalAnother['id'] = child.userId;
        tempData.push(objMedicalAnother);
      }
      this.data = tempData;
    });
  }

  convertStartDate() {
    this.newDateStart = this.modelGroupForm.value.dateStartVisit.year
      + '-' + this.convertDayTime(this.modelGroupForm.value.dateStartVisit.month)
      + '-' + this.convertDayTime(this.modelGroupForm.value.dateStartVisit.day)
      + 'T' + this.convertDayTime(this.modelGroupForm.value.timeStartVisit) + ':00';
  }

  convertEndDate() {
    this.newDateEnd = this.modelGroupForm.value.dateEndVisit.year
      + '-' + this.convertDayTime(this.modelGroupForm.value.dateEndVisit.month)
      + '-' + this.convertDayTime(this.modelGroupForm.value.dateEndVisit.day)
      + 'T' + this.convertDayTime(this.modelGroupForm.value.timeEndVisit) + ':00';
  }

  convertDayTime(input) {
    if (input < 10) {
      input = '0' + input;
    }
    return input;
  }

  addVisitHistory() {
    this.dialogService.setLoaderVisible(true);
    if (this.selectedVisitMeeting === undefined) {
      this.checkValidateSelect = true;
      $('.select2-selection--single').css('border-color', '#D21D29');
    } else {
      if (this.selectedVisitMeeting === '0') {
        this.checkValidateSelect = true;
        $('.select2-selection--single').css('border-color', '#D21D29');
      } else {
        this.checkValidateSelect = false;
        $('.select2-selection--single').css('border-color', '#AAA');
      }
    }
    this.me0003Validator.checkValidate();
    this.me0003Validator.checkSpaceBlank(this.modelGroupForm);
    if (this.modelGroupForm.valid && this.checkValidateSelect === false
    && this.formErrors.nameVisitor === '' && this.formErrors.visitorOffice === ''
    && this.formErrors.visitPurpose === '' && this.formErrors.visitPlace === '' ) {
      this.convertStartDate();
      this.convertEndDate();
      this.modelGroupForm.removeControl('dateStartVisit');
      this.modelGroupForm.removeControl('dateEndVisit');
      this.modelGroupForm.removeControl('timeStartVisit');
      this.modelGroupForm.removeControl('timeEndVisit');
      this.modelResponse = this.modelGroupForm.value;
      this.modelResponse.userId = this.selectedVisitMeeting;
      this.modelResponse.start = this.newDateStart;
      this.modelResponse.end = this.newDateEnd;
      this.meetingService.postAddVisitorRecordSettings(this.modelResponse).subscribe((response: VisitEntrySettingsSaveResult) => {
        this.dialogService.setLoaderVisible(false);
      }, (error: HttpError) => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('MSG.ERROR');
      });
    } else {
      this.dialogService.setLoaderVisible(false);
    }
  }

  selectVisitMeeting(event) {
    this.selectedVisitMeeting = event.id;
  }

  genTimePicker() {
    for (let i = 0; i <= 23; i++) {
      for (let j = 0; j < 60; j += 5) {
        let a = '';
        if (i < 10) {
          if (j < 10) {
            a = '0' + i.toString() + ':0' + j.toString();
          } else {
            a = '0' + i.toString() + ':' + j.toString();
          }
        } else {
          if (j < 10) {
            a = i.toString() + ':0' + j.toString();
          } else {
            a = i.toString() + ':' + j.toString();
          }
        }
        this.timePicker.push(a);
      }
    }
  }

}



