import {Injectable, OnDestroy} from '@angular/core';
import * as firebase from 'firebase';
import {environment} from '../../../environments/environment';
import {Http, Response} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import {Subscription} from 'rxjs/Subscription';
import 'rxjs/add/observable/zip';
import 'rxjs/add/observable/concat';
import {JwtHelper} from 'angular2-jwt';
import {NGXLogger} from 'ngx-logger';
import {AuthenticationMessage} from '../message.service';
import {AuthenticationService} from '../authentication.service';
import {TimerObservable} from 'rxjs/observable/TimerObservable';

/**
 * Firebase app type.
 *
 * @author k.sumi 2017/09/19
 */
export enum FirebaseAppType {
  Database = 'firebase.database',
  Storage = 'firebase.storage',
}

/**
 * Firebaseに関連する機能を提供するクラス.
 *
 * @author k.sumi 2017/09/19
 */
@Injectable()
export class FirebaseService implements OnDestroy {

  // Variables
  // --------------------------------------------------------------------------
  // Firebase
  private database: firebase.app.App;
  private storage: firebase.app.App;
  // Subscriber
  private subscription: Subscription;
  // Lock object
  private lock: object;
  // Jwt
  private jwtHelper: JwtHelper;


  // Constructor
  // --------------------------------------------------------------------------
  constructor(private http: Http,
              private auth: AuthenticationService,
              private authMessage: AuthenticationMessage,
              private logger: NGXLogger) {
    this.initializer();
  }


  // Overrides
  // --------------------------------------------------------------------------
  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }


  // Getter
  // --------------------------------------------------------------------------
  appDatabase(): firebase.app.App {
    return this.database;
  }

  appStorage(): firebase.app.App {
    return this.storage;
  }


  // Clear token
  // --------------------------------------------------------------------------
  clearTokenAll(): Observable<boolean> {
    return Observable.concat(this.clearTokenOnStorage(), this.clearTokenOnDatabase());
  }

  clearTokenOnStorage(): Observable<boolean> {
    return this.logout(this.storage);
  }

  clearTokenOnDatabase(): Observable<boolean> {
    return this.logout(this.database);
  }

  private logout(app: firebase.app.App): Observable<boolean> {
    return Observable.create(subscriber => {
      if (app) {
        app.auth().signOut().then(() => {
          subscriber.next(true);
          subscriber.complete();
        }).catch(error => {
          subscriber.error(error);
        });
      } else {
        subscriber.next(true);
        subscriber.complete();
      }
    });
  }


  // Initilizer
  // --------------------------------------------------------------------------
  private initializer() {
    this.logger.info('Initialize FirebaseService');

    // Jwt
    this.jwtHelper = new JwtHelper();

    // Loginin notify
    this.subscription = this.authMessage.get().subscribe((type) => {
      if (type === AuthenticationMessage.Type.Loginin) {
        this.subscribeLoginin();
      }
    });

    // Init firebase
    this.database = firebase.initializeApp(environment.fbDatabase, FirebaseAppType.Database);
    this.storage = firebase.initializeApp(environment.fbStorage, FirebaseAppType.Storage);

    // Check login
    if (this.auth.isLogin()) {
      this.signinAll();
    }
  }


  // Loginin subscription
  // --------------------------------------------------------------------------
  protected subscribeLoginin() {
    this.logger.info('## Loginned subscription');
    this.signinAll();
  }


  // Signin ALL
  // --------------------------------------------------------------------------
  signinAll(groupId = 'any') { // TODO: groupId=anyは要検討
    if (this.lock) {
      return;
    }
    this.lock = {};

    Observable.zip(this.refreshTokenOnDatabase(), this.refreshTokenOnStorage(groupId))
      .subscribe((names: string[]) => {
        this.lock = null;
        this.logger.info('OK firebase, ', names);
      }, (error) => {
        this.lock = null;
        this.logger.error(error);
      });
  }


  // Database
  // --------------------------------------------------------------------------
  refreshTokenOnDatabase(): Observable<any> {
    const api = '/uaa/fb/group/access/token';

    return Observable.create((subscriber) => {
      this.http.get(api).subscribe((res: Response) => {
        this.database.auth().signInWithCustomToken(res.text()).then(() => {
          subscriber.next(FirebaseAppType.Database);
          subscriber.complete();
          // expire
          const date = this.jwtHelper.getTokenExpirationDate(res.text());
          this.scheduleRefreshToken(date, FirebaseAppType.Database);

        }).catch((error) => {
          subscriber.error(error);
        });
      }, (error) => {
        subscriber.error(error);
      });
    });
  }


  // Storage
  // --------------------------------------------------------------------------
  refreshTokenOnStorage(groupId = 'any'): Observable<any> { // TODO: groupId=anyは要検討
    const api = `/uaa/fb/storage/access/token?gid=${groupId}`;

    return Observable.create((subscriber) => {
      this.http.get(api).subscribe((res: Response) => {
        this.storage.auth().signInWithCustomToken(res.text()).then(() => {
          subscriber.next(FirebaseAppType.Storage);
          subscriber.complete();
          // expire
          const date = this.jwtHelper.getTokenExpirationDate(res.text());
          this.scheduleRefreshToken(date, FirebaseAppType.Storage, groupId);

        }).catch((error) => {
          subscriber.error(error);
        });
      }, (error) => {
        subscriber.error(error);
      });
    });
  }


  // Refresh token schedule
  // --------------------------------------------------------------------------
  private scheduleRefreshToken(initialInterval: number | Date = 0, type: FirebaseAppType, groupId?: string) {
    this.logger.info('## Initial interval=', initialInterval);

    TimerObservable.create(initialInterval).subscribe(() => {
      this.obtainObservable(type, groupId).subscribe(name => {
        this.logger.info('### Refresh firebase access token=', name);
      }, () => {
        this.scheduleRefreshToken(5000, type, groupId);
      });
    });
  }

  private obtainObservable(type: FirebaseAppType, groupId?: string): Observable<any> {
    if (type === FirebaseAppType.Database) {
      return this.refreshTokenOnDatabase();
    }
    return this.refreshTokenOnStorage(groupId);
  }
}



