import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'app-face-icon',
  templateUrl: './face-icon.component.html',
  styleUrls: ['./face-icon.component.scss']
})
export class FaceIconComponent implements OnInit {
  @Input() officeUserId: string;
  @Input() iconSize: number = 30;
  // iconSize: 30 or 40 or 55 or 76 or 94
  public iconPath: string = '/assets/img/user-no-image.png';
  public firstName: string;
  public lastName: string;

  constructor() { }

  ngOnInit() {
    // TODO mock
    this.iconPath = '/assets/img/user.png';
    this.firstName = '太郎';
    this.lastName = '山田';
  }

}



