import {Injectable} from '@angular/core';
import {FormArray, FormBuilder, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {NgbDateMomentParserFormatter} from '../shared/ngb-date-moment-parser-formatter';
declare const moment: any;

@Injectable()
export class CreatePollSurveyValidator {
  validationMessages: any;
  checkValidForm = true;
  checkValidArray = true;
  parserFormatter = new NgbDateMomentParserFormatter();
  formErrors = {
    contents: '',
    singleChoiceFlag: null,
    choices: [],
    closeDate: '',
    closeTime: ''
  };

  constructor(private translate: TranslateService, private fb: FormBuilder) {
  }

  createForm() {
    return this.fb.group({
      'contents': ['', Validators.compose([Validators.required, Validators.maxLength(2000)])],
      'singleChoiceFlag': [null, Validators.required],
      'choices': this.fb.array([]),
      'closeDate': [''],
      'closeTime': ['']
    });
  }

  checkValidate(form, formErrors) {
    this.checkValidForm = true;
    this.checkValidArray = true;
    let stringDate = '';
    const currentDate = moment().format('YYYY/MM/DD');
    const currentTime = moment().format('HH:mm');
    if (!form) {
      return;
    } else {
      this.getValidationMessage();
      for (const field in formErrors) {
        switch (field) {
          case 'choices':
            this.arrayValidate(form, 'choices');
            continue;
          case 'closeDate':
            formErrors['closeDate'] = '';
            if (form.get('closeDate').value === '') {
              formErrors['closeDate'] = this.validationMessages['closeDate'].required;
              this.checkValidForm = false;
            }
            if (moment(this.parserFormatter.format(form.get('closeDate').value), 'YYYY/MM/DD', true).isValid()
              || moment(form.get('closeDate').value, 'YYYY/MM/DD', true).isValid()) {
              const ngbDate = form.get('closeDate').value;
              if (typeof ngbDate === 'object') {
                stringDate = this.parserFormatter.format(ngbDate);
              } else {
                stringDate = ngbDate;
              }
              stringDate = moment(stringDate).format('YYYY/MM/DD');
              if (!moment(stringDate).isSameOrAfter(currentDate)) {
                formErrors['closeDate'] = this.validationMessages['closeDate'].pattern;
                this.checkValidForm = false;
              }
            } else {
              formErrors['closeDate'] = this.validationMessages['closeDate'].pattern;
              this.checkValidForm = false;
            }
            continue;
          case 'closeTime':
            formErrors['closeTime'] = '';
            if (form.get('closeTime').value === '') {
              formErrors['closeTime'] = this.validationMessages['closeTime'].required;
              this.checkValidForm = false;
            }
            if (moment(form.get('closeTime').value, 'HH:mm', true).isValid()) {
              const selectedTime = form.get('closeTime').value;
              if (moment(stringDate).isSame(currentDate) && selectedTime < currentTime) {
                console.log(moment(stringDate).isSame(currentDate))
                this.formErrors['closeTime'] = this.validationMessages['closeTime'].pattern;
                this.checkValidForm = false;
              }
            } else {
              this.formErrors['closeTime'] = this.validationMessages['closeTime'].pattern;
              this.checkValidForm = false;
            }
            continue;
          default:
            formErrors[field] = '';
            const control = form.get(field);
            if (control && control.invalid) {
              const mess = this.validationMessages[field];
              for (const key in control.errors) {
                if (control.errors.hasOwnProperty(key)) {
                  formErrors[field] = mess[key];
                  this.checkValidForm = false;
                }
              }
            } else {
              formErrors[field] = '';
            }
        }
      }
      console.log(formErrors)
      return {formErrors: formErrors, checkValidForm: this.checkValidForm, checkValidArray: this.checkValidArray};
    }
  }

  arrayValidate(form, field) {
    if (!form) {
      return;
    } else {
      this.getValidationMessage();
      const list = <FormArray>form.get(field);
      for (let i = 0; i < list.controls.length; i++) {
        const control = list.controls[i];
        const mess = this.validationMessages[field];
        if (control && !control.valid) {
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field][i] = mess[key];
              this.checkValidArray = false;
            }
          }
        } else {
          this.formErrors[field][i] = '';
        }
      }
      return this.formErrors[field];
    }
  }


  getValidationMessage() {
    this.translate.get('GR0009.GR0009_VALIDATION').subscribe((res) => {
      this.validationMessages = {
        'contents': {
          'required': res.CONTENTS.REQUIRED,
          'maxlength': res.CONTENTS.RANGE
        },
        'singleChoiceFlag': {
          'required': res.NON_SELECTED
        },
        'choices': {
          'required': res.CHOICES.REQUIRED,
          'maxlength': res.CHOICES.RANGE
        },
        'closeDate': {
          'required': res.CLOSE_DATE.REQUIRED,
          'pattern': res.CLOSE_DATE.WRONG_FORMAT
        },
        'closeTime': {
          'required': res.CLOSE_TIME.REQUIRED,
          'pattern': res.CLOSE_TIME.WRONG_FORMAT
        }
      };
    });
  }

}



