
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Subject} from 'rxjs/Subject';
import {FirGroupTopics} from '../models/gr/firebase/fir.group.topics';

/**
 * @author k.sumi 2017/09/23
 */
export abstract class Message<T> {

  // Subject
  protected subject = new Subject<T>();

  /** Send message */
  send(msg: T) {
    this.subject.next(msg);
  }

  /** Get message */
  get(): Observable<T> {
    return this.subject.asObservable();
  }

  /** Clear message */
  clear() {
    this.subject.next();
  }
}

/**
 * Authenticate Message.
 *
 * @author k.sumi 2017/09/23
 */
@Injectable()
export class AuthenticationMessage extends Message<AuthenticationMessage.Type> {
}

/**
 * Authenticate Message Type.
 */
export namespace AuthenticationMessage {
  export enum Type {
    Loginin,
    Logout,
    Refresh,
  }
}

/**
 * Topics Group Message.
 */
@Injectable()
export class TopicsGroupMessage extends Message<FirGroupTopics> {
}



