/**
 * ログインチェック
 */
@Injectable()
export class CanActivateViaAuthGuard implements CanActivate {
  constructor(private sharedValue: SharedValueService, private router: Router) {
  }

  canActivate() {
    if (this.sharedValue.isLoggedIn()) {
      return this.sharedValue.fetchUserSession();
    } else {
      this.router.navigate(['/login'], { replaceUrl: true });
      return false;
    }
  }
}


