import {Component, OnInit} from '@angular/core';
import {CalendarService} from '../../../services/calendar.service';
import {EventCreateSettings} from '../../../models/ca/event-create-settings';
import {FormGroup} from '@angular/forms';
import {Ca0004PageValidator} from './ca0004-page.validator';
import {EventCreateSettingValidator} from '../../../models/ca/event-create-setting-validator';
import {Router} from '@angular/router';
import {LocalStorage} from '../../../services/local-storage.service';
import {TranslateService} from '@ngx-translate/core';
import {EventCreateSettingsSaveResult} from '../../../models/ca/event-create-settings-save-result';
import {DialogService} from '../../../services/dialog.service';

@Component({
  selector: 'app-ca0004-page',
  templateUrl: './ca0004-page.component.html',
  styleUrls: ['./ca0004-page.component.scss'],
  providers: [Ca0004PageValidator]
})
export class Ca0004PageComponent implements OnInit {
  createEventForm: FormGroup;
  formModel = new EventCreateSettingValidator();
  formMessagesError = {
    'startDay': false,
    'startTime': false,
    'endDay': false,
    'endTime': false,
    'timeMatch': false
  };
  public model = new EventCreateSettings();
  listStaff: any;
  options = [
    {'value': 'VISIBLE', 'key': 'HOSPITAL_VALUE_1'},
    {'value': 'INVISIBLE', 'key': 'HOSPITAL_VALUE_2'}
  ];
  valueShare = {'user': 'MY', 'insideHospital': 'INSIDE_GROUP', 'outsideHospital': 'OUTSIDE_GROUP'};
  checkAllDay = false;
  timePicker = [];

  constructor(private calendarService: CalendarService, private localStorage: LocalStorage, private  router: Router,
              private ca0004PageValidator: Ca0004PageValidator, private translateService: TranslateService,
              private dialogService: DialogService) {
  }

  ngOnInit() {
    this.genTimePicker();
    this.getListStaff();
    this.createEventForm = this.ca0004PageValidator.createForm(this.formModel);
  }

  getListStaff() {
    this.calendarService.getEventCreateSettings('STAFF').subscribe((settings: EventCreateSettings) => {
      this.translateService.get('CA004').subscribe((response) => {
        this.listStaff = Object.keys(settings).map(key => settings[key]);
        this.listStaff = this.listStaff[0];
        this.listStaff.splice(0, 0, {
          'userId': '',
          'firstName': response.SHARE_USER.VALUE_DEFAULT,
          'lastName': ''
        });
      }, (error) => {
        this.dialogService.showError('CA004.MSG.CA0004_E000_1');
      });
    });
  }

  postEventCreateSettings(settings: EventCreateSettingsSaveResult) {
    this.ca0004PageValidator.checkValidate(this.createEventForm, this.formMessagesError);
    if (this.createEventForm.valid && this.formMessagesError.timeMatch === false) {
      this.dialogService.setLoaderVisible(true);
      this.formModel = this.createEventForm.value;
      this.ca0004PageValidator.mapModel(this.formModel, this.model);
      console.log(this.model);
      this.calendarService.postEventCreateSettings(this.model).subscribe(response => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showSuccess('CA004.SAVED');
      }, (error) => {
        this.dialogService.setLoaderVisible(false);
        this.dialogService.showError('CA004.CA0004_E001_1');
      });
    }
  }

  genTimePicker() {
    for (let i = 0; i <= 23; i++) {
      for (let j = 0; j < 60; j += 5) {
        let a = '';
        if (i < 10) {
          if (j < 10) {
            a = '0' + i.toString() + ':0' + j.toString();
          } else {
            a = '0' + i.toString() + ':' + j.toString();
          }
        } else {
          if (j < 10) {
            a = i.toString() + ':0' + j.toString();
          } else {
            a = i.toString() + ':' + j.toString();
          }
        }
        this.timePicker.push(a);
      }
    }
  }

  displayOption(value) {
    if (value === this.valueShare.user) {
      this.formModel.optionShare = value;
      this.createEventForm.controls['userId'].enable();
      $('#public-type').prop('disabled', false);
      $('#group-inside').prop('disabled', true);
      $('#group-outside').prop('disabled', true);
    } else if (value === this.valueShare.insideHospital) {
      this.formModel.optionShare = value;
      this.createEventForm.controls['userId'].disable();
      $('#public-type').prop('disabled', true);
      $('#group-inside').prop('disabled', false);
      $('#group-outside').prop('disabled', true);
    } else if (value === this.valueShare.outsideHospital) {
      this.formModel.optionShare = value;
      this.createEventForm.controls['userId'].disable();
      $('#public-type').prop('disabled', true);
      $('#group-inside').prop('disabled', true);
      $('#group-outside').prop('disabled', false);
    }
  }

  isAllDay() {
    if (this.checkAllDay === false) {
      this.checkAllDay = true;
    } else {
      this.checkAllDay = false;
    }
  }

  editEvent() {
    const data = this.ca0004PageValidator.getDataForm(this.createEventForm);
    console.log(this.createEventForm.value);
    if (this.localStorage.getObject('ca0004')) {
      this.localStorage.remove('ca0004');
    }
    this.localStorage.setObject('ca0004', data);
    this.router.navigate(['/ca/ca0007']);
  }
}



