import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-member-select',
  templateUrl: './member-select.component.html',
  styleUrls: ['./member-select.component.scss']
})
export class MemberSelectComponent implements OnInit {
  addMemberSelect = new Array();
  removeMemberSelect = new Array();
  public addMemberAllSelectVal: boolean;

  // TODO mock
  private mockData1 = {
    list: [
      {officeId: '1', officeUserId: '1', isAdmin: false},
      {officeId: '1', officeUserId: '2', isAdmin: false},
      {officeId: '1', officeUserId: '3', isAdmin: false},
      {officeId: '1', officeUserId: '4', isAdmin: false},
      {officeId: '1', officeUserId: '5', isAdmin: false},
      {officeId: '1', officeUserId: '6', isAdmin: false},
      {officeId: '1', officeUserId: '7', isAdmin: false},
      {officeId: '1', officeUserId: '8', isAdmin: false},
      {officeId: '1', officeUserId: '9', isAdmin: false},
      {officeId: '1', officeUserId: '10', isAdmin: false},
      {officeId: '1', officeUserId: '11', isAdmin: false},
      {officeId: '1', officeUserId: '12', isAdmin: false},
      {officeId: '1', officeUserId: '13', isAdmin: false},
      {officeId: '1', officeUserId: '14', isAdmin: false},
      {officeId: '1', officeUserId: '15', isAdmin: false},
      {officeId: '1', officeUserId: '16', isAdmin: false},
      {officeId: '1', officeUserId: '17', isAdmin: false},
      {officeId: '1', officeUserId: '18', isAdmin: false},
      {officeId: '1', officeUserId: '19', isAdmin: false},
      {officeId: '1', officeUserId: '20', isAdmin: false},
      {officeId: '1', officeUserId: '21', isAdmin: false}
    ]
  };
  private mockData2 = {
    list: [
      {officeId: '1', officeUserId: '22', isAdmin: false},
      {officeId: '1', officeUserId: '23', isAdmin: true},
      {officeId: '1', officeUserId: '24', isAdmin: false}
    ]
  };

  addMemberList = this.mockData1.list;
  removeMemberList = this.mockData2.list;

  constructor() { }

  ngOnInit() {
  }

  addMemberAllSelect(event:any){
    this.addMemberList.forEach(function(member,i,array){
      this.addMemberSelect[i] = this.addMemberAllSelectVal;
    }.bind(this));
  }

  addMember(event:any){
    let newAddMemberList = [];
    this.addMemberList.forEach(function(member,i,array){
      if(this.addMemberSelect[i]){
        this.removeMemberList.push(member);
      }else{
        newAddMemberList.push(member);
      }
    }.bind(this));
    this.addMemberSelect = [];
    this.addMemberList = newAddMemberList;
    if(!this.addMemberList.length){
      this.addMemberAllSelectVal = false;
    }
  }

  removeMember(event:any){
    let newRemoveMemberList = [];
    this.removeMemberList.forEach(function(member,i,array){
      if(this.removeMemberSelect[i]){
        member.isAdmin = false;
        this.addMemberList.push(member);
      }else{
        newRemoveMemberList.push(member);
      }
    }.bind(this));
    this.removeMemberSelect = [];
    this.removeMemberList = newRemoveMemberList;
  }

  addMemberSelectClick(member:string, event:any, i:number){
    this.addMemberSelect[i] =! this.addMemberSelect[i];
  }

  removeMemberSelectClick(member:string, event:any, i:number){
    this.removeMemberSelect[i] =! this.removeMemberSelect[i];
  }

  isAdminChecked(member:string, event:any, i:number){
    this.removeMemberList[i].isAdmin =! this.removeMemberList[i].isAdmin;
  }

}



