import { Component, OnInit } from '@angular/core';
import { MeetingService } from '../../../services/meeting.service';
import { DialogService } from '../../../services/dialog.service';
import { ListVisitorHistoriesSettings } from '../../../models/me/status-meeting-settings';
import { CalendarCommon } from '../../../common/calendar-common';
declare var moment: any;
@Component({
  selector: 'app-me0024-page',
  templateUrl: './me0024-page.component.html',
  styleUrls: ['./me0024-page.component.scss']
})
export class Me0024PageComponent implements OnInit {

  public model = new ListVisitorHistoriesSettings;
  dateNow: any;
  addDate: any;
  subDate: any;
  historyday: string;
  hidenTitle = false;
  constructor(private meetingService: MeetingService, private dialogService: DialogService, private calendarCommon: CalendarCommon) { }

  ngOnInit() {
    this.dialogService.setLoaderVisible(true);
    this.meetingService.getListVisitorHistoriesSettings().subscribe((settings: ListVisitorHistoriesSettings) => {
      this.dialogService.setLoaderVisible(false);
      this.model = settings;
      console.log(this.model);
    });
    $('.hidenTitle').hide();
    /* *************************************************************** */
  }

  getHistoryNow() {
    const date = new Date();
    console.log(date);
    this.dateNow = moment(date).format('YYYY-MM-DDThh:mm');
  }

  getSubHistoryNow() {
    const up = moment(this.dateNow).clone().add(-1, 'days').format('YYYY-MM-DDThh:mm');
    this.addDate = this.calendarCommon.convertDateTimeIso8601(up);
  }

  getAddHistoryNow() {
    const down = moment(this.dateNow).clone().add(1, 'days').format('YYYY-MM-DDThh:mm');
    this.subDate = this.calendarCommon.convertDateTimeIso8601(down);
  }

  getHistoryOfDay(date: any) {
    const a = moment(this.historyday).format('YYYY-MM-DDThh:mm');
    console.log(this.historyday);
    console.log(a);
  }

  printPage() {
    const css = '@page { layout: portrait; size: landscape}, ' +
      '@media print {html, body {width: 210mm;height: 297mm;}}';
    const head = document.head || document.getElementsByTagName('head')[0];
    const style = document.createElement('style');
    style.type = 'text/css';
    style.media = 'print';

    style.appendChild(document.createTextNode(css));
    head.appendChild(style);

    $('.page-head').hide();
    $('.print-left').hide();
    $('.hidenTitle').show();
    window.print();
    $('.print-left').show();
    $('.page-head').show();
    $('.hidenTitle').hide();
    return false;
  }
  /* *************************************************************** */

}



