import {Component, OnInit} from '@angular/core';

declare const $: any;

declare const getContext: any;
declare const Chart: any;


@Component({
  selector: 'app-top-page',
  templateUrl: './top-page.component.html',
  styleUrls: ['./top-page.component.scss']
})
export class TopPageComponent implements OnInit {
  public officeUserId:string = 'test';
  constructor() {
  }

  ngOnInit() {

    // グラフテスト
    const canvas = <HTMLCanvasElement> document.getElementById('canvas');
    const ctx = canvas.getContext('2d');

    Chart.pluginService.register({
      afterDraw: function (chart) {
        if (typeof chart.config.options.lineAt !== 'undefined') {
          let lineAt = chart.config.options.lineAt;
          const ctxPlugin = chart.chart.ctx;
          const xAxe = chart.scales[chart.config.options.scales.xAxes[0].id];
          const yAxe = chart.scales[chart.config.options.scales.yAxes[0].id];

          // I'm not good at maths
          // So I couldn't find a way to make it work ...
          // ... without having the `min` property set to 0
          if (yAxe.min !== 0) {
            return;
          }

          ctxPlugin.strokeStyle = 'red';
          ctxPlugin.beginPath();
          lineAt = (lineAt - yAxe.min) * (100 / yAxe.max);
          lineAt = (100 - lineAt) / 100 * (yAxe.height) + yAxe.top;
          ctxPlugin.moveTo(xAxe.left, lineAt);
          ctxPlugin.lineTo(xAxe.right, lineAt);
          ctxPlugin.stroke();
        }
      }
    });

    const myChart = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
        datasets: [{
          data: [18, 22, 3, 5, 2, 3],
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(255, 159, 64, 0.2)'
          ],
          borderColor: [
            'rgba(255,99,132,1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)'
          ],
          borderWidth: 1
        }]
      },
      options: {
        lineAt: 16,
        scales: {
          yAxes: [{
            ticks: {
              min: 0
            }
          }]
        },
        legend: {
          display: false
        }
      }
    });

    // Select2テスト
    $('.select2:not(.js-states)').select2({
      width: '300px',
      tags: true,
      placeholder: '選択してください'
    }).on('change', function () {

    });

    $('.select2.js-states').select2({
      width: '300px',
      tags: true,
      placeholder: '選択してください'
    }).on('change', function () {

    });

    // Tooltipテスト
    $('[data-toggle="tooltip"]').tooltip();

  }

}



