import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SharedValueService } from '../../../services/shared-value.service';

@Component({
  selector: 'app-me0001-page',
  templateUrl: './me0001-page.component.html',
  styleUrls: ['./me0001-page.component.scss']
})
export class Me0001PageComponent implements OnInit {

  public meetingPresentation: any;
  public requestSource: any;
  public status: any;
  public sort: any;
  selectedMeetingPresentation = '0';
  selectedRequestSource = '0';
  selectedStatus = '0';
  selectedSort = '1';

  constructor(
    private router: Router,
    private shareValue: SharedValueService
  ) {
    this.meetingPresentation = this.shareValue.meetingTopSelectOption().meetingPresentation;
    this.requestSource = this.shareValue.meetingTopSelectOption().requestSource;
    this.status = this.shareValue.meetingTopSelectOption().status;
    this.sort = this.shareValue.meetingTopSelectOption().sort;
  }

  ngOnInit() {
  }

  changeMeetingPresentation(event) {
    console.log('aaa');
  }
  changeRequestSource(event) {
    console.log(event);
  }
  changeStatus(event) {
    console.log(event);
  }
  changeSort(event) {
    console.log(event);
  }

  goToME0016() {
    this.router.navigate(['me/me0016']);
  }

}



