import { AfterViewInit, Component, OnInit, Renderer2 } from '@angular/core';
import { Location } from '@angular/common';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { RegistrationService } from '../../../services/registration.service';
import { DialogService } from '../../../services/dialog.service';
import { StaffInvite } from '../../../models/re/staff-invite';
import { StaffInviteSettings } from '../../../models/re/staff-invite-settings';
import { SharedValueService } from '../../../services/shared-value.service';
import { Helper } from '../../../common/helper';
import { DialogResult } from '../../../models/dialog-param';
import { StaffInvitePageValidator } from './staff-invite-page.validator';
import { Validator } from '../../../common/validation/validator';
import { DepartmentSelectComponent } from '../../../components/department-select/department-select.component';
import { DepartmentSettings } from '../../../models/re/department-settings';
import { HttpError } from '../../../common/error/http.error';

declare const XLSX: any;
declare const $: any;
@Component({
  selector: 'app-staff-invite-page',
  templateUrl: './staff-invite-page.component.html',
  styleUrls: ['./staff-invite-page.component.scss'],
  providers: [StaffInvitePageValidator, Validator, DepartmentSelectComponent]
})
export class StaffInvitePageComponent implements OnInit, AfterViewInit {

  public indexTab = 0;
  public users: any;
  public cbChecked: string[] = [];
  public selectedAll: any;
  // Constant
  public pageDefault = '0';
  public sortDefault = '-lastName';
  // Params
  public department = '';
  public name = '';
  public page = '0';
  public size = '10';
  public sortBy = 'lastName';
  public sortDirection = '-';
  public sort = this.sortDirection + this.sortBy;
  public group = '0';
  // model
  public staffBulks = new StaffInviteSettings;
  public modelDepartmentSettings = Array(new DepartmentSettings());

  // Data when Scroll
  public dataScroll: any;
  public title: string;
  descriptionsMail: string;
  titleMail: string;
  public pdepartment = '';
  public listUserMail = [];
  public listUserMailFail = [];

  listDepartment = new Array();
  listJobType = new Array();
  listmanagementAuthority = new Array();
  listfunctionAuthoritySet = new Array();
  model = {};
  dataStaff = [];
  dataAddStaff = [];
  arrayErrorForm = [];
  modelGroupForm: FormGroup;
  resultValidate = true;
  countDoctor = [];
  countPharma = [];
  listDe: any;
  departSelected: string;
  officeName: string;
  authorityTwo = {};
  authority = {};
  userSession;

  constructor(private fb: FormBuilder, private translate: TranslateService, private registrationService: RegistrationService,
    private dialogService: DialogService, private renderer: Renderer2,
    private sharedValueService: SharedValueService, private helper: Helper, private validation: StaffInvitePageValidator,
    private departmentSelectComponent: DepartmentSelectComponent) {
    this.sharedValueService.fetchUserSession();
    this.userSession = this.sharedValueService.getUserSession();
    this.officeName = this.userSession.officeName;
    this.authority = {
      managementAuthority: this.userSession.managementAuthority,
      deptId: this.userSession.deptId,
      objAll: {
        displayName: 'すべて',
        id: '',
        level: 1,
        name: 'すべて',
        save: true,
        text: '選択してください',
      },
      obj: {
        displayName: 'すべて',
        id: '',
        level: 1,
        name: 'すべて',
        save: true,
        text: '選択してください',
      }
    };
    this.authorityTwo = {
      managementAuthority: this.userSession.managementAuthority,
      deptId: this.userSession.deptId,
      objAll: {
        displayName: 'すべて',
        id: '',
        level: 1,
        name: 'すべて',
        save: true,
        text: 'すべて',
      },
      obj: null
    };
    this.modelGroupForm = this.fb.group({
      'staff': this.fb.array([]),
    });
    const staff = new StaffInvite('', '', '', '', { id: '', name: '', displayName: '', children: [] }, '', '', '', '');
    this.dataStaff.push(staff);
    this.addStaff(this.dataStaff);
  }

  ngOnInit() {
    this.getStaffBulkSettings();
    this.translate.get('RE0015').subscribe(res => {
      this.titleMail = res.VALUE_SUBJECT;
      this.descriptionsMail = res.VALUE_ARTICLE;
    });
    this.listDepartment = this.departmentSelectComponent.list;
    this.listJobType = this.sharedValueService.getJopTypeFake();
    const authority1 = this.sharedValueService.getAuthority();
    this.listmanagementAuthority = authority1.adminAuthority;
    this.listfunctionAuthoritySet = authority1.functionAuthority;
    this.genDepartmentSelect();
  }

  ngAfterViewInit() {
    $('[data-toggle="tooltip"]').tooltip();
    $('[data-toggle="popover"]').popover();
  }

  get modelStaff() {
    return <FormArray>this.modelGroupForm.get('staff');
  }

  preventDefault(event) {
    this.dataAddStaff = new Array();
    event.preventDefault();
    this.dataAddStaff.push(new StaffInvite('', '', '', '', { id: '', name: '', displayName: '', children: [] }, '', '', '', ''));
    this.addStaff(this.dataAddStaff);
  }

  addStaff(listData: any) {
    const arrayStaff = <FormArray>this.modelGroupForm.get('staff');
    for (let i = 0; i < listData.length; i++) {
      const newStaff = this.validation.createStaff(listData[i]);
      arrayStaff.push(newStaff);
      this.arrayErrorForm.push(new StaffInvite('', '', '', '', '', '', '', '', ''));
    }
    console.log(this.modelGroupForm.get('staff').value);
  }

  deleteAllStaff() {
    let listStaff = <FormArray>this.modelGroupForm.get('staff');
    const length = listStaff.controls.length;
    if (listStaff.controls.length > 0) {
      for (let index = 0; index < length; index++) {
        listStaff.removeAt(0);
        listStaff = <FormArray>this.modelGroupForm.get('staff');
      }
    }
    this.arrayErrorForm.splice(0, this.arrayErrorForm.length);
  }

  deleteStaff(index: number) {
    const arrayStaff = <FormArray>this.modelGroupForm.get('staff');
    arrayStaff.removeAt(index);
    this.arrayErrorForm.splice(index, 1);
  }
  /**
   * RE0015 get list department from api
   */
  genDepartmentSelect() {
    this.registrationService.getDepartmentSettings().subscribe(
      (res: DepartmentSettings[]) => {
        this.modelDepartmentSettings = res;
        const step = 1;
        this.listDe = this.helper.createArrayDepartment([], this.modelDepartmentSettings, '', step);
      },
      (error: HttpError) => {
        this.dialogService.showError('MSG.ERROR');
      });
  }
  changeDepartment(department) {
    const listStaff = this.modelGroupForm.get('staff') as FormArray;
    const staff = <FormGroup>listStaff.controls[department.index];
    this.departSelected = department.departmentId;
    if (staff.get('department').value !== department.departmentId) {
      staff.controls['department'].setValue(department.department);
    } else {
      // do nothing
    }
  }

  changeJobType(jobType, index) {
    if (this.countDoctor[index] === undefined && jobType === 'J0001') {
      this.countDoctor[index] = 1;
      this.countPharma[index] = 0;
    } else if (jobType === 'J0001') {
      this.countDoctor[index]++;
    }
    if (this.countPharma[index] === undefined) {
      this.countPharma[index] = 1;
      this.countDoctor[index] = 0;
    } else if (jobType === 'J0022') {
      this.countPharma[index]++;
    }
    if (jobType === 'J0022' && this.countPharma[index] === 1) {
      $('.admin' + '_' + index).val('MP_2');
      $('.function' + '_' + index).val('FPS_2');
    }
    if (jobType === 'J0001' && this.countDoctor[index] === 1) {
      $('.admin' + '_' + index).val('');
      $('.function' + '_' + index).val('FPS_3');
    }
  }

  registerStaff() {
    if (!this.modelGroupForm) {
      return;
    }
    console.log(this.modelGroupForm.value)
    const form = this.modelGroupForm;
    const listStaff = <FormArray>form.get('staff');
    if (listStaff.controls.length > 0) {
      for (let index = 0; index < listStaff.controls.length; index++) {
        this.validation.checkValidate(index, null, form, this.arrayErrorForm);
        const result = this.validation.checkValidate(index, null, form, this.arrayErrorForm);
        this.resultValidate = result;
      }
      if (this.modelGroupForm.valid && this.resultValidate) {
        this.confirmRegister(listStaff.value);
      }
    } else {
      this.dialogService.showError('MSG.STAFF_SELECT');
    }
  }
  // Re0015_2
  confirmRegister(listStaff: FormArray) {
    const title = 'スタッフを登録しますか？';
    const html = '<p>' + listStaff.length + '名のユーザーを' + this.officeName + '病院 のスタッフとして登録します。' + '<p>';
    this.dialogService.showMessage('warning', false, title, null, html, null, 'MSG.YES', 'MSG.NO').subscribe(
      (res: DialogResult) => {
        if (res.buttonName === Helper.dialog_result_ok) {
          this.staffInviteTempRegister();
        }
      }
    );
  }

  /**
   * Call Api invite staff in tap staffInvite Re0015_3
   */
  staffInviteTempRegister() {
    this.model = this.modelGroupForm.controls.staff.value;
    this.dialogService.setLoaderVisible(true);
    this.registrationService.staffInviteTempRegist(this.model).subscribe(
      res => {
        this.dialogService.setLoaderVisible(false);
        if (res.status === 200) {
          this.confirmSentMail();
          // xem lại data trả về là array staff hay là list email
        }
      },
      error => {
        this.dialogService.setLoaderVisible(false);
      }
    );
  }

  confirmSentMail() {
    const title = '仮登録が完了しました';
    const html = '仮登録したユーザーに本登録を促すメールを送信しますか？<br><br>後ほど全体管理者の都合の良いタイミングで本登録を促すこともできます。<br><br>※後日メールを送信する場合は「管理者メニュー」の「一括招待」で行うことができます。また、メールアドレスを登録していないユーザーには「管理者メニュー」の「一括招待」にて仮パスワードを印刷することができます。';
    this.dialogService.showMessage(null, false, title, null, html, null, 'MSG.YES', 'MSG.NO').subscribe(
      (dialogResult: DialogResult) => {
        if (dialogResult.buttonName === Helper.dialog_result_ok) {
          this.staffInviteSendEmail();
        } else {
          if (dialogResult.buttonName === Helper.dialog_result_cancel) {
            this.deleteAllStaff();
            this.addStaff(this.dataStaff);
          }
        }
      }
    );
  }

  /**
   * Call Api send email in tap staffInvite
   */
  staffInviteSendEmail() {
    this.model = this.modelGroupForm.controls.staff.value;
    this.dialogService.setLoaderVisible(true);
    this.registrationService.staffInviteSendEmail(this.model).subscribe(
      res => {
        this.dialogService.setLoaderVisible(false);
        if (res.status === 200) {
          this.dialogService.showSuccess('MSG.INVITATION_COMPLETE').subscribe(
            (dialogResult: DialogResult) => {
              if (dialogResult.buttonName === Helper.dialog_result_ok) {
                this.deleteAllStaff();
                this.addStaff(this.dataStaff);
              }
            });
        }
        if (res.status === 400) {
          const title = 'RE0015.ERROR_INVITE';
          const html = this.getHtmlSweetAlert('', res.staff);
          this.dialogService.showMessage(null, false, title, null, html, null, 'BTN.OK', null).subscribe(
            (dialogResult: DialogResult) => {
              if (dialogResult.buttonName === Helper.dialog_result_ok) {
                this.deleteAllStaff();
                this.addStaff(this.dataStaff);
              }
            });
        }
      },
      error => {
        this.dialogService.setLoaderVisible(false);
      }
    );
  }

  getHtmlSweetAlert(title: string, listStaff: any) {
    let html = '<p class="warning-message">' + title + '</p>'
      + '<table id="table" border=0 class="table">';
    for (let index = 0; index < listStaff.length; index++) {
      html += '<tr>'
        + '<td>'
        + '<div class="surname-wrapper">'
        + '<span class="surname-kana">' + listStaff[index]['firstName'] + '</span>'
        + '<span class="surname">' + listStaff[index]['firstNameKana'] + '</span>'
        + '</div>'
        + '</td>'
        + '<td>'
        + '<div class="name-wrapper">'
        + '<span class="name-kana">' + listStaff[index]['firstNameKana'] + '</span>'
        + '<span class="name">' + listStaff[index]['lastName'] + '</span>'
        + '</div>'
        + '</td>'
        + '<td>'
        + listStaff[index]['email']
        + '</td>'
        + '</tr>';
    }
    html += '</table>';
    return html;
  }

  /**
   * import file xlsx in tap staffInvite
   * @param event1
   */
  onChangeFile(event1) {
    const datas = [];
    const files = event1.target.files;
    const file = files[0];
    const reader = new FileReader();
    reader.readAsArrayBuffer(file);

    reader.onload = f => {
      const contents: any = f.target;
      const data = contents.result;
      const arr1 = String.fromCharCode.apply(null, new Uint8Array(data));
      const workbook = XLSX.read(btoa(arr1), { type: 'base64' });
      const first_sheet_name = workbook.SheetNames[0];
      const worksheet = workbook.Sheets[first_sheet_name];
      const rangen = worksheet['!ref'];
      const utils = XLSX.utils;
      const rangeVal = utils.decode_range(rangen);
      for (let r = 9; r <= rangeVal.e.r; r++) {
        if (datas.length === Helper.max_import_staff_invite) {
          break;
        }
        const row = {};

        const firstName = worksheet[utils.encode_cell({ c: 0, r: r })];
        const lastName = worksheet[utils.encode_cell({ c: 1, r: r })];
        const firstNameKana = worksheet[utils.encode_cell({ c: 2, r: r })];
        const lastNameKana = worksheet[utils.encode_cell({ c: 3, r: r })];
        const dept = worksheet[utils.encode_cell({ c: 4, r: r })];
        const job_type = worksheet[utils.encode_cell({ c: 5, r: r })];
        const mail = worksheet[utils.encode_cell({ c: 6, r: r })];
        const managementAuthority = worksheet[utils.encode_cell({ c: 7, r: r })];
        const functionAuthoritySet = worksheet[utils.encode_cell({ c: 8, r: r })];
        if (!(firstName && lastName && firstNameKana && lastNameKana && dept && job_type && managementAuthority && functionAuthoritySet)) {
          continue;
        }
        const depart_id = this.listDe.filter(item => item.name === dept.v)[0];
        const job_id = this.listJobType.filter((item) => item.Name === job_type.v)[0];
        const managementAuthority_id = this.listmanagementAuthority.filter((item) => item.name === managementAuthority.v)[0];
        const functionAuthoritySet_id = this.listfunctionAuthoritySet.filter((item) => item.name === functionAuthoritySet.v)[0];

        if (!(job_id && managementAuthority_id && functionAuthoritySet_id)) {
          continue;
        }
        row['firstName'] = (firstName) ? firstName.v : '';
        row['lastName'] = (lastName) ? lastName.v : '';
        row['firstNameKana'] = (firstNameKana) ? firstNameKana.v : '';
        row['lastNameKana'] = (lastNameKana) ? lastNameKana.v : '';
        const dep = (depart_id) ? (depart_id.id) : '';
        row['department'] = { id: dep, name: '', displayName: '', children: [] };
        row['jobType'] = (job_id) ? job_id.Id : '';
        row['email'] = (mail) ? mail.v : '';
        row['managementAuthority'] = (managementAuthority_id) ? managementAuthority_id.id : '';
        row['functionAuthoritySet'] = (functionAuthoritySet_id) ? functionAuthoritySet_id.id : '';
        datas.push(row);
      }
      if (datas.length > 0) {
        this.deleteAllStaff();
        this.addStaff(datas);
      } else {
        // do nothing
      }
    };
  }

  downloadCVS() {
    const xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function () {
      if (this.readyState === 4 && this.status === 200) {
        const blob = new Blob([this.response], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
        if (window.navigator.msSaveOrOpenBlob) {
          window.navigator.msSaveOrOpenBlob(blob, 'staff_invite.xlsx');
        } else {
          const url = URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = 'staff_invite.xlsx';
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
        }
      }
    };
    xhttp.open('GET', '../../../../assets/examples/RE0015.xlsx', true);
    xhttp.responseType = 'arraybuffer';
    xhttp.send();
  }

  getListMsg(msg: string) {
    const msgText = [];
    this.translate.get(msg).subscribe(
      res => {
        for (const field in res) {
          if (res.hasOwnProperty(field)) {
            msgText.push(res[field]);
          }
        }
      }
    );
    return msgText;
  }

  // Load all data
  getStaffBulkSettings() {
    this.dialogService.setLoaderVisible(true);
    this.registrationService.getStaffInviteSettings(this.pdepartment, this.name, this.page, this.size).subscribe(
      (settings: StaffInviteSettings) => {
        console.log(settings)
        this.dialogService.setLoaderVisible(false);
        this.staffBulks = settings;
        this.dataScroll = this.staffBulks['staffsProvisional'];
        if (this.page === this.pageDefault) {
          this.users = this.staffBulks['staffsProvisional'];
        } else {
          this.users = this.users.concat(this.dataScroll);
        }
      }, error => {
        this.dialogService.setLoaderVisible(false);
      });
  }
  showTextDepartment(index) {
    return this.helper.convertLevelDepartment('', this.users[index].department);
  }

  // Check all user
  checkALl(isChecked: boolean) {
    for (let i = 0; i < this.users.length; i++) {
      const q = this.cbChecked.indexOf(this.users[i]);
      if (isChecked) {
        if (q === -1) {
          this.cbChecked.push(this.users[i]);
        }
      } else {
        this.cbChecked.splice(q, 1);
      }
      this.users[i].selected = this.selectedAll;
    }

  }

  // User is checked
  checkIfChecked(index, ev) {
    const i = this.cbChecked.indexOf(this.users[index]);
    if (ev.target.checked) {
      if (i === -1) {
        this.cbChecked.push(this.users[index]);
      }
    } else {
      if (i !== -1) {
        this.cbChecked.splice(i, 1);
      }
    }

    this.selectedAll = this.users.forEach(function (item: any) {
      return item.selected === true;
    });
    if (this.users.length === this.cbChecked.length) {
      this.selectedAll = true;
    } else {
      this.selectedAll = false;
    }
  }

  // Search by Department
  searchDepartment(department) {
    this.page = this.pageDefault;
    this.sort = this.sortDefault;
    this.pdepartment = department.departmentId;
    this.getStaffBulkSettings();
  }

  // Search by Name
  searchUser(inputName) {
    this.page = this.pageDefault;
    this.sort = this.sortDefault;
    inputName ? (this.name = inputName) : (this.name = '');
    this.getStaffBulkSettings();
  }

  // Send email
  sendMail() {
    if (this.cbChecked.length === 0) {
      this.dialogService.showError('RE0015.CHOOSE_MAIL');
    } else {
      $('#myModal').modal('show');
    }
  }

  // complete send mail
  completeSendMail() {
    setTimeout(function () { $('#myModal01').modal('hide'); }, 300);
    const self = this;
    this.cbChecked.filter(item => {
      if (self.validateEmail(item['mailAddress'])) {
        self.listUserMail.push(item);
      } else {
        self.listUserMailFail.push(item);
      }
    });
    // waiting for API
    // to do
  }

  // check email valid
  validateEmail(emailField) {
    const reg = Helper.emailRegex;
    if (reg.test(emailField) === false) {
      return false;
    }
    return true;
  }

  // comfirm send mail
  comfirmSendMail() {
    setTimeout(function () { $('#myModal').modal('hide'); }, 300);
    $('#myModal01').modal('show');
  }

  // back send mail
  backsendingMail() {
    setTimeout(function () { $('#myModal01').modal('hide'); }, 300);
    $('#myModal').modal('show');
  }

  // Print user
  printUser(printSection) {
    if (this.cbChecked.length === 0) {
      this.dialogService.showError('RE0015.CHOOSE_MAIL');
    } else {
      let printContents, popupWin;
      printContents = document.getElementById(printSection).innerHTML;
      popupWin = window.open('', '_blank', 'height=100%,width=auto,scrollbars=no,menubar=no,toolbar=no,location=no,status=no,titlebar=no');
      popupWin.document.open();
      popupWin.document.write(
        `<html>
        <head>
          <style  media="print" type="text/css">
      body {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
       }
      .title-head{
        border-bottom: 2px solid #f08d00;
        padding: 10px 20px 0;
        margin: 10px 0 0;
      h1{
          font-weight: 700;
        }
      }
      .group-txt {
        .group-box{
        background: #eee;
        padding: 12px;
        border-radius: 0.3rem;
        }
      }
    * {
        box-sizing: border-box;
        -moz-box-sizing: border-box;
      }
        .page {
            width: 210mm;
            min-height: 297mm;
            padding: 3mm;
            margin: 3mm auto;
            background: white;
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
        }
        .subpage {
            padding: 1cm;
            height: 257mm;
        }
        
        @page {
            size: A4;
            margin: 0;
        }
        @media print {
            html, body {
                width: 210mm;
                height: 297mm;        
            }
            .group-box {
              background: #eee !important;
              padding: 12px;
              border-radius: 0.3rem;
              display:block;
              -webkit-print-color-adjust: exact; 
            }
        .page {
            margin: 0;
            border: initial;
            border-radius: initial;
            width: initial;
            min-height: initial;
            box-shadow: initial;
            background: initial;
            page-break-after: always;
          }
          .table {
            width:100%;
          }
          .table-form-txt th {
            width:150px;
          }
          .table-form-txt td {
            color:#ccc;
          }
          .table-form-txt th, .table-form-txt td {
            padding: 0.75rem;
            vertical-align: top;
          }
          .table-form-txt .boder td, .table-form-txt .boder th {
            border-top: 1px solid #D7D7D7;
          }
        }
          </style>
        </head>
      <body onload="window.print();window.close()">
        ${printContents}
      </body>
      </html>`
      );
      popupWin.document.close();
    }
  }

  // Export to CSV
  exportCSV() {
    // call API export CSV
  }

  // Scroll Paging
  scrollPaging() {
    this.renderer.listen('window', 'scroll', (evt) => {
      if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight && this.indexTab === 1) {
        this.page = String(+this.page + 1);
        this.getStaffBulkSettings();
      }
    });
  }

  changeTab(indexTab) {
    this.indexTab = indexTab;
    if (indexTab === 1) {
      this.page = this.pageDefault;
      this.department = '';
      this.getStaffBulkSettings();
    }
  }
}



