import { Component, OnInit } from '@angular/core';
import { MeetingService } from '../../../services/meeting.service';
import { ListMeeetings } from '../../../models/me/list-meeting-settings';
import { Subscription } from 'rxjs/Subscription';
import { ActivatedRoute, Router, Routes } from '@angular/router';
import { CalendarCommon } from '../../../common/calendar-common';
import { AcceptMeetingsSendApi } from '../../../models/me/accept-meetings-send-api';
import { DialogService } from '../../../services/dialog.service';
import { DialogResult } from '../../../models/dialog-param';
import { Helper } from '../../../common/helper';
import { DelayMeetingSettings } from '../../../models/me/delay-meeting-settings';
import { CancelMeetingsSendApi } from '../../../models/me/cancel-meeting-settings';

@Component({
  selector: 'app-me0007-page',
  templateUrl: './me0007-page.component.html',
  styleUrls: ['./me0007-page.component.scss']
})
export class Me0007PageComponent implements OnInit {
  public settings = new ListMeeetings();
  flagStatusOne: boolean;
  flagMain: boolean;
  flagStatusTwo: boolean;
  statusFrame: boolean;
  flagDelay: boolean;
  flagCancel: boolean;
  flagRejectFrame: boolean;
  flagAcceptFrame: boolean;
  flagFrame: boolean;
  typeId: string;
  requestId: string;
  subscription: Subscription;
  frameMeeting: any;
  frameMeetingDetail: any;
  requestMeeting: any;
  startDate = [];
  endDate = [];
  startDateOptions = [];
  endDateOptions = [];
  startTime: string;
  endTime: string;
  selectedTime: number;
  optionStartDate: string;
  optionEndDate: string;
  start: any;
  end: any;
  place: string;
  delayDate = [];
  dateDelay: string;
  contentMessage: string;
  reQuestId: string;
  drId: string;
  mrId: string;
  date: string;
  dayOfMeet: string;
  index = 0;

  constructor(private meetingService: MeetingService,
    public activatedRoute: ActivatedRoute,
    private calendarCommon: CalendarCommon,
    private dialogService: DialogService,
    private router: Router,
    private calendar: CalendarCommon) { }

  ngOnInit() {
    this.selectedTime = 0;
    this.subscription = this.activatedRoute.params.subscribe(params => {
      this.typeId = params['typeId'];
      this.requestId = params['requestId'];
    });
    this.getMeetingSettings();
    this.flagStatusOne = false;
    this.flagMain = true;
    this.statusFrame = false;
    this.flagDelay = false;
    this.flagCancel = false;
    this.flagAcceptFrame = false;
    this.flagRejectFrame = false;
    this.flagFrame = true;
    this.delayDate = ['10', '20', '30', '40', '50', '60'];
    this.contentMessage = '今回は都合により面会予定の調整ができませんでした。またご応募いただければ幸いです。今後とも宜しくお願い致します。';
    this.dateDelay = '';
  }
  getMeetingSettings() {
    this.meetingService.getMeetingsSettings(this.typeId, this.requestId).subscribe((settings: ListMeeetings) => {
      console.log('settings:', settings);
      if (this.typeId === '1') {
        this.frameMeeting = settings['frameMeetingInfo'];
        this.frameMeetingDetail = this.frameMeeting.candidates;
      } else if (this.typeId === '0') {
        this.requestMeeting = settings.requestInfo;
        for (let i = 0; i < this.requestMeeting.candidateTimes.length; i++) {
          const startdate = this.requestMeeting.candidateTimes[i].startTime;
          const enddate = this.requestMeeting.candidateTimes[i].endTime;
          const start = this.calendarCommon.convertDatetime(startdate);
          const end = this.calendarCommon.convertDatetime(enddate);
          if (i === 0) {
            this.optionStartDate = start.time;
            this.optionEndDate = end.time;
          }
          this.start = start;
          this.end = end;
          this.startDate.push(start);
          this.endDate.push(end);
          this.startDateOptions.push(this.convertTime(start.time, 0));
          this.endDateOptions.push(this.convertTime(end.time, 1));
        }
      }
    });
    this.place = '';
  }

  acceptMeeting() {
    this.flagMain = false;
    this.flagStatusOne = true;
  }

  backCancelReject() {
    this.flagMain = true;
    this.flagDelay = false;
  }

  rejectMeeting() {
    this.flagMain = false;
    this.flagStatusTwo = true;

  }

  changeDateMeeting() {
    this.router.navigate(['mr/me0006']);
  }

  putAcceptMeeting(requestId, mrId, drId) {
    this.optionStartDate = this.requestMeeting.candidateTimes[this.index].startTime;
    this.optionEndDate = this.requestMeeting.candidateTimes[this.index].endTime;
    const model = new AcceptMeetingsSendApi(requestId, '', '0', this.optionStartDate, this.optionEndDate, this.place);
    this.dialogService.setLoaderVisible(true);
    this.meetingService.putAcceptMeetingSettings(model).subscribe(response => {
      this.dialogService.showMessage('success', false, '面会を承認しました', '取引先との細かいやりとりをするため、連絡ルームに移動しますか？', null, null, 'Yes', 'No').subscribe(
        (resp: DialogResult) => {
          this.dialogService.setLoaderVisible(false);
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.router.navigate(['ch/ch001', mrId, drId]);
          } else if (resp.buttonName === Helper.dialog_result_cancel) {
            this.getMeetingSettings();
            this.place = '';
            this.selectedTime = 0;
            this.backDetailReject();
          }
        });
    });
  }

  sendMessageChat(mrId, drId) {
    this.router.navigate(['ch/ch001', mrId, drId]);
  }

  delayMeeting() {
    this.flagMain = false;
    this.flagDelay = true;
  }

  putDelayMeetingSettings(requestId) {
    const model = new DelayMeetingSettings(requestId, this.dateDelay);
    console.log(model)
    this.meetingService.putDelayMeetingSettings(model).subscribe(response => {
      this.dialogService.showSuccess('面会相手に遅れる旨のメッセージを送信しました').subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.dialogService.setLoaderVisible(false);
            this.getMeetingSettings();
            this.place = '';
            this.selectedTime = 0;
            this.backCancelReject();
          }
        });
    });
  }

  backDetail() {
    this.flagMain = true;
    this.flagStatusOne = false;
    this.flagDelay = false;
  }

  putRejectMeetingSettings(requestId) {
    const model = new CancelMeetingsSendApi(requestId, '', '0', this.contentMessage, '', '');
    this.meetingService.putRejectMeetingSettings(model).subscribe(response => {
      this.dialogService.showSuccess('面会リクエストを断りました').subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.dialogService.setLoaderVisible(false);
            this.getMeetingSettings();
            this.place = '';
            this.selectedTime = 0;
            this.backDetailReject();
          }
        });
    });
  }

  backDetailReject() {
    this.flagMain = true;
    this.flagStatusTwo = false;
    this.flagAcceptFrame = false;
    this.flagRejectFrame = false;
    this.flagCancel = false;
    this.flagFrame = true;
    this.flagStatusOne = false;
    this.flagDelay = false;
  }

  cancelMeeting() {
    this.flagMain = false;
    this.flagCancel = true;
  }

  putCacelMeetingSettings(requestId) {
    const model = new CancelMeetingsSendApi(requestId, '', '0', this.contentMessage, '', '');
    console.log(model)
    this.meetingService.putCancelMeetingSettings(model).subscribe(response => {
      this.dialogService.showSuccess('面会を中止しました').subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.dialogService.setLoaderVisible(false);
            this.getMeetingSettings();
            this.dateDelay = '0';
            this.backDetailReject();
          }
        });
    });
  }

  acceptFrameMeeting(requestId, mrId, drId) {
    this.reQuestId = requestId;
    this.drId = drId;
    this.mrId = mrId;
    console.log(requestId, mrId, drId);
    this.flagAcceptFrame = true;
    this.flagRejectFrame = false;
    this.flagMain = false;
    this.flagCancel = false;
    this.flagFrame = false;
  }

  rejectFrameMeeting(requestId, mrId, drId) {
    this.reQuestId = requestId;
    this.drId = drId;
    this.mrId = mrId;
    console.log(requestId, mrId, drId);
    this.flagAcceptFrame = false;
    this.flagRejectFrame = true;
    this.flagCancel = false;
    this.flagMain = false;
    this.flagFrame = false;
  }

  putCancelFrameMeetingSettings(frameId) {
    console.log(this.reQuestId, this.mrId, this.drId);
    const model = new CancelMeetingsSendApi(this.reQuestId, frameId, '1', this.contentMessage, '', '');
    console.log(model)
    this.meetingService.putCancelMeetingSettings(model).subscribe(response => {
      this.dialogService.showSuccess('面会を中止しました').subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.dialogService.setLoaderVisible(false);
            this.contentMessage = '';
            this.router.navigate(['me/me0001']);
            this.getMeetingSettings();
          }
        });
    });
  }

  putAcceptFrameMeetingSettings(frameId) {
    const model = new AcceptMeetingsSendApi(this.reQuestId, frameId, '1', '', '', '');
    console.log(model);
    this.meetingService.putAcceptMeetingSettings(model).subscribe(response => {
      this.dialogService.showMessage('success', false, '面会を承認しました', '取引先との細かいやりとりをするため、連絡ルームに移動しますか？', null, null, 'Yes', 'No').subscribe(
        (resp: DialogResult) => {
          if (resp.buttonName === Helper.dialog_result_ok) {
            this.router.navigate(['ch/ch001', this.mrId, this.drId]);
          } else if (resp.buttonName === Helper.dialog_result_cancel) {
            this.place = '';
            this.selectedTime = 0;
            this.router.navigate(['me/me0001']);
            this.getMeetingSettings();
          }
        });
    });
    console.log(this.reQuestId, this.mrId, this.drId);

  }
  convertTimeToString(hour: number, minute: number) {
    const m = minute < 10 ? '0' + minute : minute;
    const h = hour < 10 ? '0' + hour : hour;
    return (h + ':' + m);
  }

  convertTime(value: string, operator: number) {
    let arrValue;
    let returnArr;
    returnArr = [];
    arrValue = value.split(':');
    for (let i = 0; i < arrValue.length; i++) {
      arrValue[i] = parseInt(arrValue[i], 0);
    }
    returnArr.push(this.convertTimeToString(arrValue[0], arrValue[1]));
    if (operator === 0) {
      for (let i = 0; i < 2; i++) {
        arrValue[1] += 5;
        if (arrValue[1] === 60) {
          arrValue[0] += 1;
          arrValue[1] = 0;
        }
        returnArr.push(this.convertTimeToString(arrValue[0], arrValue[1]));
      }
    } else {
      for (let i = 0; i < 2; i++) {
        if (arrValue[1] === 0) {
          arrValue[0] -= 1;
          arrValue[1] = 55;
        } else if (arrValue[1] > 0) {
          arrValue[1] -= 5;
        }
        returnArr.unshift(this.convertTimeToString(arrValue[0], arrValue[1]));
      }
    }
    return returnArr;
  }

  printPage() {
    const css = '@page { layout: portrait; size: A3}, ' +
      '@media print {html, body {width: 210mm;height: 297mm;}}';
    const head = document.head || document.getElementsByTagName('head')[0];
    const style = document.createElement('style');
    style.type = 'text/css';
    style.media = 'print';

    style.appendChild(document.createTextNode(css));
    head.appendChild(style);

    $('#print-page').hide();
    window.print();
    $('#print-page').show();
    return false;
  }

  enableOption(index) {
    this.index = index;
    this.selectedTime = index;
    this.optionStartDate = $('#startDate_' + index).val();
    this.optionEndDate = $('#endDate_' + index).val();
  }

  changeStart(event, index) {
    this.index = index;
    const day = $('.dayOf_' + this.index).text();
    this.optionStartDate = $('#startDate_' + index).val();
  }

  changeEnd(event, index) {
    this.index = index;
    const day = $('.dayOf_' + this.index).text();
    this.optionEndDate = $('#endDate_' + index).val();

  }
}



