import {environment} from '../../environments/environment';
import {Configuration, DefaultHeaders, NgxOAuthClient, NgxOAuthResponse} from 'ngx-oauth-client';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/observable/throw';
import {HttpClient, HttpHeaders, HttpResponse} from '@angular/common/http';
import {JwtHelper} from 'angular2-jwt';
import {AuthenticationMessage} from './message.service';
import {Profile} from '../common/profile';

// TODO: 仮実装

/**
 * AuthenticationService.
 */
@Configuration({
  // host: 'http://shielded-anchorage-91142.herokuapp.com',
  host: environment.api,
  token: 'uaa/oauth/token',
  key: 'demo',
  secret: 'demo',
})
@DefaultHeaders({
  'Content-Type': 'application/json',
  'Accept': 'application/json',
})
export class AuthenticationService extends NgxOAuthClient {

  // Instance variables
  // --------------------------------------------------------------------------
  private jwtHelper = new JwtHelper();


  // Constructor
  // --------------------------------------------------------------------------
  constructor(http: HttpClient, private authMessage: AuthenticationMessage) {
    super(http);
  }


  // Overrided
  // --------------------------------------------------------------------------
  isLogin(): boolean {
    try {
      const accessToken = this.fetchToken('access_token');
      return accessToken && 0 < accessToken.length;

    } catch (e) {
      // ignore
    }
    return false;
  }

  getToken(grant_type?: string, data?: any): Observable<NgxOAuthResponse> {
    // mock?
    if (environment.profile === Profile.mock || environment.profile === Profile.demo) {
      return this.http.get('/assets/mock/uaa/oauth/token.json').map((res: NgxOAuthResponse) => {
        this.saveToken(res);
        return res;
      });
    }

    if (grant_type && ['password', 'refresh_token'].indexOf(grant_type) === -1) {
      throw new Error(`Grant type ${grant_type} is not supported`);
    }
    const config = this.getConfig();
    const defaults: any = {
      grant_type: grant_type || 'password'
    };

    const payload = Object.assign(defaults, data);
    const params: string[] = [];
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        params.push(`${key}=${payload[key]}`);
      }
    }

    const url = config.host + '/' + config.token;
    const headers = new HttpHeaders({
      'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8',
      'Authorization': 'Basic ' + btoa(config.key + ':' + config.secret),
    });

    return this.http.post(url, params.join('&'), {headers}).map((res: NgxOAuthResponse) => {
      // set token
      this.setToken(res);

      // save token
      this.saveToken(res);

      const accessToken = this.fetchToken('access_token');
      console.log('access_token', accessToken);
      
      localStorage.setItem('access_token', accessToken);
      const decoded = this.jwtHelper.decodeToken(accessToken);
      console.log('decoded: ', decoded);

      // notify
      this.authMessage.send(AuthenticationMessage.Type.Loginin);

      // return response
      return res;
    });
  }

  private saveToken(res: NgxOAuthResponse) {
    console.log(res);
    // persistence
    localStorage.setItem('auth_token', JSON.stringify(res));
  }

  // @Override
  clearToken(): void {
    super.clearToken();
  }

  // @override
  requestInterceptor(request: any): any {
    console.log('### request interceptor');

    const token = this.fetchToken('access_token');
    if (token) {
      return request.setHeaders({
        'Authorization': 'Bearer ' + token,
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      });
    }
    return request;
  }

  // @override
  errorInterceptor(request: any, error: any): any {
    console.log('### error interceptor');
    console.log(error);

    if (error.status === 401) {
      const refreshToken = this.fetchToken('refresh_token');
      if (!refreshToken) {
        return Observable.throw(error);
      }
      return this.getToken('refresh_token', {refreshToken}).switchMap(token => {
        return this.getClient().request(request.method,
          request.url,
          this.requestInterceptor(request.setHeaders({Authorization: 'Bearer ' + token})));
      });
    }
    return Observable.throw(error);
  }
}



