import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-pr-office-select',
  templateUrl: './pr-office-select.component.html',
  styleUrls: ['./pr-office-select.component.scss']
})
export class PrOfficeSelectComponent implements OnInit {
  @Input() officeId: string;
  @Input() prOfficeSelectOptions: any = {
    'multiple': true,
    'allowClear': true,
    'minimumInputLength': 0,
    'maximumInputLength': 5,
    'maximumSelectionLength': 5,
    'disabled': false
  };

  @Output() prOfficeSelected = new EventEmitter<any>();
  selected: any;
  data: any[];
  remoteOptions: any;

  constructor(private translate: TranslateService) { }

  ngOnInit() {

    // TODO mock
    this.data = [
      {'text': '会社1', 'id': '1001','disabled': true},
      {'text': '会社2', 'id': '1002','selected': true},
      {'text': '会社3', 'id': '1003'},
      {'text': '会社4', 'id': '1004'},
      {'text': '会社5', 'id': '1005'},
      {'text': '会社6', 'id': '1006'},
      {'text': '会社7', 'id': '1007'},
      {'text': '会社8', 'id': '1008'},
      {'text': '会社9', 'id': '1009'},
      {'text': '会社10', 'id': '1010'},
      {'text': '会社11', 'id': '1011'},
      {'text': '会社12', 'id': '1012'},
      {'text': '会社13', 'id': '1013'},
      {'text': '会社14', 'id': '1014'},
      {'text': '会社15', 'id': '1015'},
      {'text': '会社16', 'id': '1016'},
      {'text': '会社17', 'id': '1017'},
      {'text': '会社18', 'id': '1018'},
      {'text': '会社19', 'id': '1019'},
      {'text': '会社20', 'id': '1020'}
    ];

    let language = [];
    this.translate.get('PR_OFFICE_SELECT').subscribe((msg) => {
      language = msg;
    });

    this.remoteOptions = {
      language: {
        inputTooShort: function(args) {
          let remainingChars = args.minimum - args.input.length;
          return language['INPUT_TOO_SHORT'].replace(/{{value}}/g,remainingChars);
        },
        inputTooLong: function(args) {
          let overChars = args.input.length - args.maximum;
          return language['INPUT_TOO_LONG'].replace(/{{value}}/g,overChars);
        },
        errorLoading: function() {
          return language['ERROR_LOADING'];
        },
        loadingMore: function() {
          return language['LOADING_MORE'];
        },
        noResults: function() {
          return language['NO_RESULTS'];
        },
        searching: function() {
          return language['SEARCHING'];
        },
        maximumSelected: function(args) {
          return language['MAXIMUM_SELECTED'].replace(/{{value}}/g,args.maximum);
        }
      },
      placeholder: language['PLACEHOLDER'],
      width: '100%',
      minimumResultsForSearch: Infinity
    }

    Object.keys(this.prOfficeSelectOptions).forEach(function(key){
      if(this.prOfficeSelectOptions[key]){
        this.remoteOptions[key] = this.prOfficeSelectOptions[key];
      }
    }.bind(this));

  }

  changeFields(val:any[]){
    this.prOfficeSelected.emit(val);
  }

}



