import { TranslateService } from '@ngx-translate/core';
import { Injectable } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { Helper } from '../../../common/helper';
@Injectable()

export class Me0003PageValidator {

  me0003PageForm: FormGroup;
  formErrors = {
    'dateStartVisit': '',
    'timeStartVisit': '',
    'dateEndVisit': '',
    'timeEndVisit': '',
    'nameVisitor': '',
    'visitorOffice': '',
    'visitorNumber': '',
    'visitPurpose': '',
    'visitPlace': '',
    'visitNote': ''
  };
  vaidationForm: any = {};
  validationMessages: any = {};
  errorBranch: string;
  errorBranchAddress: string;

  constructor(
    private fb: FormBuilder,
    private translate: TranslateService,
    private helper: Helper
  ) {
    this.createForm();
  }

  createForm() {
    this.me0003PageForm = this.fb.group({
      'dateStartVisit': ['', [Validators.required]],
      'timeStartVisit': ['', [Validators.required]],
      'dateEndVisit': ['', [Validators.required]],
      'timeEndVisit': ['', [Validators.required]],
      'nameVisitor': ['', [Validators.required, Validators.maxLength(30)]],
      'visitorOffice': ['', [Validators.required, Validators.maxLength(30)]],
      'visitorNumber': ['', [Validators.required]],
      'visitPurpose': ['', [Validators.required, Validators.maxLength(50)]],
      'visitPlace': ['', [Validators.required, Validators.maxLength(30)]],
      'visitNote': ['', [Validators.maxLength(4000)]]
    });
  }

  checkSpaceBlank(form) {
    if (form.value.nameVisitor.trim() === '') {
      this.formErrors.nameVisitor = this.validationMessages.nameVisitor.required;
    }
    if (form.value.visitorOffice.trim() === '') {
      this.formErrors.visitorOffice = this.validationMessages.visitorOffice.required;
    }
    if (form.value.visitPurpose.trim() === '') {
      this.formErrors.visitPurpose = this.validationMessages.visitPurpose.required;
    }
    if (form.value.visitPlace.trim() === '') {
      this.formErrors.visitPlace = this.validationMessages.visitPlace.required;
    }
  }

  checkValidate() {
    if (!this.me0003PageForm) {
      return false;
    }
    this.getErrorValidate();
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        this.formErrors[field] = this.helper.validateForm(this.me0003PageForm, field, this.validationMessages[field]);
      }
    }
  }

  getErrorValidate() {
    this.translate.get('ME0003.VALIDATE').subscribe((res: string) => {
      this.vaidationForm = res;
      this.validationMessages = {
        'dateStartVisit': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'timeStartVisit': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'dateEndVisit': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'timeEndVisit': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'nameVisitor': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'visitorOffice': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'visitorNumber': {
          'required': this.vaidationForm.REQUIRED
        },
        'visitPurpose': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'visitPlace': {
          'required': this.vaidationForm.REQUIRED,
          'maxlength': this.vaidationForm.REQUIRED,
          'pattern': this.vaidationForm.REQUIRED
        },
        'visitNote': {
          'maxlength': this.vaidationForm.REQUIRED
        }
      };
    });
  }
}



