  // Firebase.
  // --------------------------------------------------------------------------
  getTopicGroups(): Observable<FirGroupTopics[]> {
    return Observable.create(subscriber => {
      const path = '/user/1000/groupInfo'; // TODO: sample

      this.database.fetch(FirGroupTopics, path).subscribe((groups) => {
        // group info
        const obs: Observable<FirGroupTopics>[] = groups.map(data => {
          return Observable.create(subscribe => {
            this.database.fetchOne(FirGroup, `/group/${data.id}`).subscribe(group => {
              data.group = group;
              subscribe.next(data);
              subscribe.complete();
            });
          });
        });
        // find
        Observable.forkJoin(obs).subscribe((array) => {
          subscriber.next(GroupService.sortGroups(array));
          subscriber.complete();
        }, error => {
          subscriber.error(error);
        });

      }, error => {
        subscriber.error(error);
      });
    });
  }

