  constructor(private http: Http) {
  }

  // ME0003
  getVisitableUsersSettings(officeId): Observable<VisitEntrySettings> {
    return new Observable(observer => {
      this.http.get('/api/dr/me/get_visitable_users?officeId=' + officeId).subscribe((response: Response) => {
        observer.next(Object.assign(new VisitEntrySettings(), response.json()));
        observer.complete();
      });
    });
  }

  postAddVisitorRecordSettings(settings: VisitEntrySettings): Observable<VisitEntrySettingsSaveResult> {
    return new Observable(observer => {
      this.http.post('/api/dr/me/add_visitor_record', JSON.stringify(settings))
        .subscribe((response: Response) => {
          if (response.status === HttpStatusCode.HTTP_POST_SUCCESS) {
            console.log(settings);
            observer.next(Object.assign(new VisitEntrySettingsSaveResult(), response.json()));
            observer.complete();
          } else {
            observer.error();
          }
        }, (error) => {
          observer.error(error.json());
        });
    });
  }

  // ME0007
  getMeetingsSettings(typeId, requestId): Observable<ListMeeetings> {
    return new Observable(observer => {
      this.http.get('/api/dr/me/get_meeting?typeId=' + typeId + '&requestId=' + requestId).subscribe((response: Response) => {
        if (response.status === HttpStatusCode.HTTP_GET_SUCCESS) {
          observer.next(Object.assign(new ListMeeetings(), response.json()));
          observer.complete();
        } else {
          observer.error();
        }
      }, (error) => {
        observer.error(error);
      });
    });
  }

