import {Component, OnInit} from '@angular/core';
import {SharedValueService} from '../../services/shared-value.service';
import {PopupHover, PopupHoverResult} from '../../models/popup-hover';
import {Subject} from 'rxjs/Subject';

declare const $: any;

@Component({
  selector: 'app-popup-hover',
  templateUrl: './popup-hover.component.html',
  styleUrls: ['./popup-hover.component.scss']
})
export class PopupHoverComponent implements OnInit {
  public popup = new PopupHover(null, null, null, null, null, null, false, false, new Subject());
  public showPopup = true;
  public top = 0;
  public left = 0;
  public showTop = true;
  public maxHeight: number;
  public leftPopup = 0;
  public topPopup = 0;
  public position: any;

  constructor(private sharedService: SharedValueService) {
  }

  ngOnInit() {
    this.maxHeight = this.get_calendar_height();
    this.sharedService.showPopupHover.subscribe(popup => {
      this.popup = popup;
       const target = $(this.popup.jsEvent.target);
        const top = target.offset().top + target.height();
        let left = target.offset().left;

        const position = new Array();
        $('.fc-day-header').each(function( index ) {
          const positionClass = {};
          positionClass['left'] = $(this).offset().left;
          position.push(positionClass);
        });

        const widthDayCalendar = $('.fc-day-header').width();
        $('#popupHover').show();
        this.showPopup = false;

        for (let i = 0; i < position.length; i++) {
          if (position[i].left <= left && left <= (position[i].left + widthDayCalendar)) {
            left = position[i].left + widthDayCalendar / 2;
            break;
          }
        }

      setTimeout(() => {
        const heightPopup = $('.contentPopupHover').height();

        if ((heightPopup + top + 10) < this.maxHeight) {
          this.showTop = true;
          this.leftPopup = left - $('.contentPopupHover').width() / 2;
          this.topPopup = top;

        } else {
          this.showTop = false;
          this.leftPopup = left - $('.contentPopupHover').width() / 2;
          this.topPopup = top - target.height() - heightPopup - 5;
        }

        $('#popupHover').css({
          'position': 'absolute',
          'z-index': 1000,
          'left': this.leftPopup,
          'top': this.topPopup,
        });
      });
    });


    $(document).click((e) => {
      if (!this.showPopup) {
        const top = $('.contentPopupHover').offset().top;
        const bottom = $('.contentPopupHover').offset().top + $('.contentPopupHover').height();

        const left = $('.contentPopupHover').offset().left;
        const right = $('.contentPopupHover').offset().left + $('.contentPopupHover').width();

        if (!(e.clientX <= right && e.clientX >= left && e.clientY <= bottom && e.clientY >= top)) {
          this.onClickButton('close');
        }
      }
    });

    $(window).resize(() => {
      this.maxHeight = this.get_calendar_height();
    });
  }

  close() {
    this.popup = new PopupHover(null, null, null, null, null, null, false, false, new Subject());
    this.showPopup = true;
    $('#popupHover').hide();
  }

  onClickButton(nameBtn) {
    this.popup.subject.next(new PopupHoverResult(nameBtn));
    this.popup.subject.complete();
    this.close();
  }

  get_calendar_height() {
    return $(window).height();
  }
}



