import { Component, OnInit } from '@angular/core';
import { DialogService } from '../../../services/dialog.service';
import { GroupService } from '../../../services/group.service';
import {HttpStatusCode} from '../../../common/http_status_code';
import {DialogResult} from '../../../models/dialog-param';
import { Subscription } from 'rxjs/Rx';
import { ActivatedRoute } from '@angular/router';
import {GroupOutsidePrepareJoinSetting} from '../../../models/gr/group-outside-prepare-join-settings';
import {GroupOutsideSaveResult} from '../../../models/gr/group-outside-save-result';
import {Helper} from '../../../common/helper';
import {HttpError} from '../../../common/error/http.error';

@Component({
  selector: 'app-gr0008-page',
  templateUrl: './gr0008-page.component.html',
  styleUrls: ['./gr0008-page.component.scss']
})
export class Gr0008PageComponent implements OnInit {

  subscription: Subscription;
  groupId: string;
  flagExpire: boolean;
  infoPrepare = new GroupOutsidePrepareJoinSetting();

  constructor(private groupService: GroupService,
              private dialogService: DialogService,
              public activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.subscription = this.activatedRoute.params.subscribe(params => {
      this.groupId = params['groupId'];
    });
    this.dialogService.setLoaderVisible(true);
    this.groupService.getMemberPrepare(this.groupId).subscribe((res: GroupOutsidePrepareJoinSetting) => {
      this.dialogService.setLoaderVisible(false);
      this.infoPrepare = res;
      this.flagExpire = false;
    },
    (error: HttpError) => {
      if (error.contains('COMMON.EXPIRE')) {
        this.flagExpire = true;
      } else if (error.contains('COMMON.ACCEPTED')) {
        // REDIRECT to list post group
      } else if (error.contains('COMMON.DISAGREED')) {
        this.dialogService.showError('MSG.ERROR');
      }else {
        this.dialogService.showError('MSG.ERROR');
      }
    });
  }

  agreeJoinGroup() {
    this.dialogService.setLoaderVisible(true);
    this.groupService.putJoinAgreeGroup(this.groupId).subscribe((res: GroupOutsideSaveResult) => {
      this.dialogService.setLoaderVisible(false);
      this.checkResponApi(res.status, 'agree', 'GR0008.ACCEPT');
    },
    (error: HttpError) => {
      if (error.contains('COMMON.SAVE_FAILED')) {

      } else {
        this.dialogService.showError('MSG.ERROR');
      }
    });
  }

  disagreeJoinGroup() {
    this.dialogService.setLoaderVisible(true);
    this.groupService.putJoinDisagreeGroup(this.groupId).subscribe((re: GroupOutsideSaveResult) => {
      this.dialogService.setLoaderVisible(false);
      this.checkResponApi(re.status, 'disagree', 'GR0008.DI_AGREE');
    },
    (error: HttpError) => {
      if (error.contains('COMMON.SAVE_FAILED')) {

      } else {
        this.dialogService.showError('MSG.ERROR');
      }
    });
  }

  checkResponApi(status, type, message) {
    if (status === HttpStatusCode.HTTP_PUT_SUCCESS && type === 'agree') {
      this.dialogService.showMessage('success', true, null, message, null, null, 'MSG.YES', null).subscribe(
        (res: DialogResult) => {
          setTimeout(() => {
            if (res.buttonName === Helper.dialog_result_ok) {
              // redirect to time line group
            }
          }, 400);
        }
      );
    }else if (status === HttpStatusCode.HTTP_PUT_SUCCESS && type === 'disagree') {
      this.dialogService.showMessage('success', true, null, message, null, null, 'MSG.YES', null).subscribe(
        (res: DialogResult) => {
          setTimeout(() => {
            if (res.buttonName === Helper.dialog_result_ok) {
              // redirect to time line group
            }
          }, 400);
        }
      );
    }
  }
}



